import { Sprite_Battler, Sprite } from 'rmmv'

export const overrideSpriteBattler = () => {
    const { setupDamagePopup, updateDamagePopup } = Sprite_Battler.prototype

    Sprite_Battler.prototype.setupDamagePopup = function() {
        if (!this._damagePopupBaseSprite) {
            this._damagePopupBaseSprite = new Sprite()
            this._damagePopupBaseSprite.z = 100
            this.parent.addChild(this._damagePopupBaseSprite)
        }
        setupDamagePopup.call(this)
        this._damages.forEach(damage => {
            this._damagePopupBaseSprite.addChild(damage)
        })
    }

    Sprite_Battler.prototype.updateDamagePopup = function() {
        this.setupDamagePopup()
        if (this._damages.length > 0) {
            for (let i = 0; i < this._damages.length; i++) {
                this._damages[i].update()
            }
            if (!this._damages[0].isPlaying()) {
                this._damagePopupBaseSprite.removeChild(this._damages[0])
                this._damages.shift()
            }
        }
    }

    return { setupDamagePopup, updateDamagePopup }
}
