import { $gameActors, $gameParty, $gameVariables, BattleManager, $gameSystem, $gameMap, DataManager } from 'rmmv'
import { isFunction } from './isFunction'

// cache binded functions
const bindedFunctions = {}
let cacheSaveData = []

/**
 * Get binded strings in given string.
 *
 * @param {string} str
 */
export const getBindStrings = str => str.match(/\${[^{}]+}/gi) || []

/**
 * Reduce paths to actual value in object
 *
 * @param {string[]} paths
 * @param {*} object
 */
export const reduceBindObject = (paths, object) =>
    paths.reduce((prevObject, nextPath) => {
        const next = prevObject[nextPath]
        return isFunction(next) ? next.call(prevObject) : next
    }, object)

/**
 * @param {string} bindObject
 * @param {string[]} paths
 * @param {number} index
 * @param {*} passObject
 */
export const bindToString = (bindObject, paths, index = 0, passObject = null) => {
    switch (bindObject) {
        case 'party':
            return reduceBindObject(paths, $gameParty)
        case 'system':
            return reduceBindObject(paths, $gameSystem)
        case 'map':
            return reduceBindObject(paths, $gameMap)
        case 'variables':
            return $gameVariables.value(paths[0])
        case 'data':
            return reduceBindObject(paths, passObject)
        case 'index':
            return `${index}`
        case 'actor':
        case 'currentActor':
            return reduceBindObject(
                paths,
                ($gameParty.inBattle() ? BattleManager.actor() : $gameParty.menuActor()) ||
                    $gameActors.actor(index + 1),
            )
        default:
            throw new Error(`Cannot bind with ${JSON.stringify({ bindObject, paths, index, passObject })}`)
    }
}

export const getSaveData = () => {
    const saveData = DataManager.loadGlobalInfo()
        .slice(1)
        .filter(data => data)
    cacheSaveData = saveData.concat(Array(DataManager.maxSavefiles() - saveData.length).fill(null))
}

const getInjectedObjects = (index = 0, passObject = null) => ({
    party: $gameParty,
    system: $gameSystem,
    map: $gameMap,
    variables: $gameVariables,
    data: passObject,
    index,
    actor: $gameParty.inBattle() ? BattleManager.actor() : $gameParty.menuActor() || $gameActors.actor(index + 1),
    currentActor: $gameParty.inBattle()
        ? BattleManager.actor()
        : $gameParty.menuActor() || $gameActors.actor(index + 1),
    saveData: cacheSaveData,
})

/**
 * Get correct string for binded string. Index is required for drawing
 * a list of lunatic items for Window_Selectable or Window_Command.
 *
 * @param {string} str
 * @param {number} index
 */
export const convertBindString = (str, index = 0, passObject = null, parent = null) => {
    const bindedStr = str.slice(2, -1) // remove ${} from string
    const injectedObjects = getInjectedObjects(index, passObject)
    injectedObjects.parent = parent // inject parent window
    const injectedParams = Object.keys(injectedObjects)
    /* eslint-disable no-new-func */
    const bindedFunction =
        bindedFunctions[bindedStr] ||
        new Function(
            ...injectedParams,
            `return !!(${bindedStr}.call && ${bindedStr}.apply) ? ${bindedStr}() : ${bindedStr}`,
        )
    /* eslint-enable no-new-func */
    bindedFunctions[bindedStr] = bindedFunction

    const result = bindedFunction(...Object.values(injectedObjects))
    return isFunction(result) ? result() : result
}

/**
 * Process string and replace all binded string with actual value.
 *
 * @param {string} str
 * @param {number} index
 * @param {*} passObject
 */
export const processBindString = (str, index = 0, passObject = null, parent = null) => {
    const binded = getBindStrings(str)
    return binded
        .map(bindedStr => convertBindString(bindedStr, index, passObject, parent))
        .reduce((prevText, replace, id) => prevText.replace(binded[id], replace), str)
}
