/**
 * @param {string} key
 * @param {any} presets
 */
export const getPresetValue = (key, presetName, presets) => {
    const keyPath = key.split('.')
    return keyPath.reduce((prev, cur) => prev[cur], presets[presetName])
}

/**
 * @param {string} key
 * @param {any} config
 * @param {any} presets
 */
export const getValue = (key, config, presets) => {
    const keyPath = key.split('.')
    const configValue = keyPath.reduce((prev, cur) => prev[cur], config)
    if (configValue === 'default' || configValue === undefined) {
        return getPresetValue(key, 'default', presets)
    }
    return configValue
}
