import { Sprite_Animation } from 'rmmv'

export const overrideSpriteAnimation = () => {
    const { updatePosition } = Sprite_Animation.prototype

    Sprite_Animation.prototype.updatePosition = function() {
        updatePosition.call(this)
        if (this._animation !== 3) {
            this.x += this._target.width * (0.5 - this._target.anchor.x)
            this.y += this._target.height * (1 - this._target.anchor.y)
        }
    }

    return { updatePosition }
}
