/**
 * @param {Luna.SceneConfig} config
 * @return {Luna.ComponentConfig}
 */
export const createSceneContainerConfig = config => ({
    position: {
        x: {
            type: 'fixed',
            value: 0,
        },
        y: {
            type: 'fixed',
            value: 0,
        },
        width: {
            type: 'percentage',
            value: 100,
        },
        height: {
            type: 'percentage',
            value: 100,
        },
    },
    components: config,
})
