import { Sprite_Actor, SceneManager } from 'rmmv'
import { actorSprites } from '@/utils/globalVars'

export const overrideSpriteActor = () => {
    const { setBattler, update, setupDamagePopup, damageOffsetX, setHome } = Sprite_Actor.prototype

    Sprite_Actor.prototype.setBattler = function(battler) {
        setBattler.call(this, battler)
        if (!battler) {
            return
        }
        this.setupHUDOnly()
        this.lunaName = `ActorSprite${battler.index()}`
        actorSprites[battler.actorId()] = this
        SceneManager.getContainer().addNamedChild(this)
    }

    Sprite_Actor.prototype.setupHUDOnly = function() {
        if (!this._actor.isHUDOnly()) {
            return
        }
        this._mainSprite.visible = false
        this._shadowSprite.visible = false
        this._weaponSprite.visible = false
    }

    Sprite_Actor.prototype.update = function() {
        update.call(this)
        if (!this._actor) {
            return
        }
        this.updateHUDOnly()
    }

    Sprite_Actor.prototype.updateHUDOnly = function() {
        if (!this._actor.isHUDOnly()) {
            return
        }
        this._mainSprite.visible = false
        this._shadowSprite.visible = false
        this._weaponSprite.visible = false
    }

    Sprite_Actor.prototype.setupDamagePopup = function() {
        const oldLength = this._damages.length
        setupDamagePopup.call(this, sprite)
        if (this._damages.length === oldLength) {
            return
        }
        const sprite = this._damages[this._damages.length - 1]
        sprite.setParent(this._damagePopupBaseSprite)
    }

    Sprite_Actor.prototype.damageOffsetX = function() {
        if (!this._actor.isHUDOnly()) {
            return damageOffsetX.call(this)
        }
        return 0
    }

    Sprite_Actor.prototype.setHome = function(x, y) {
        setHome.call(this, x, y)
        if (!this._actor) {
            return
        }
        this._actor.homePosition = { x, y }
    }

    return { setBattler, update, setupDamagePopup, damageOffsetX, setHome }
}
