import { Window_ActorCommand } from 'rmmv'
import { getImprovements } from '@/managers'
import { actorSprites } from '@/utils/globalVars'

export const overrideWindowActorCommand = () => {
    const { setup } = Window_ActorCommand.prototype

    Window_ActorCommand.prototype.setup = function(actor) {
        setup.call(this, actor)
        if (!actor.homePosition) {
            return
        }
        const stickyPos = getImprovements().BattleCommandStickyPosition
        if (stickyPos === 'default') {
            return
        }
        const actorSprite = actorSprites[actor.actorId()]
        this.setParent(actorSprite)
        let { x } = getImprovements().BattleCommandStickyOffsets
        let { y } = getImprovements().BattleCommandStickyOffsets
        const { _mainSprite } = actorSprite
        if (stickyPos === 'top') {
            x = -this.width / 2
            y -= _mainSprite.height + this.height
        }
        if (stickyPos === 'bottom') {
            x = -this.width / 2
        }
        if (stickyPos === 'left') {
            x -= this.width + _mainSprite.width / 2
            y -= _mainSprite.height / 2 + this.height / 2
        }
        if (stickyPos === 'right') {
            x += _mainSprite.width / 2
            y -= _mainSprite.height / 2 + this.height / 2
        }
        this.x = x
        this.y = y
    }

    return { setup }
}
