import { ImageManager } from 'rmmv'
import { SpriteLunatic } from './SpriteLunatic'
import { getBindStrings, processBindString } from '@/utils/bindData'

export class SpriteGaugeImage extends SpriteLunatic {
    initialize() {
        super.initialize()

        this.imageName = ''
        this.hue = 0
        this.rate = 1
        this.verticalGauge = false
        this.bitmap = ImageManager.loadEmptyBitmap()
    }

    setupBindFields({ rate }) {
        this.bindFields = getBindStrings(rate || '')
        this.bindValues = this.getBindValues()
    }

    prepare({ imageName, hue, scale, rate, vertical = false }) {
        this.imageName = processBindString(imageName, this.itemIndex, this.passObject, this.parentWindow)
        this.hue = hue || 0
        this.scale.x = scale ? scale.x : 1
        this.scale.y = scale ? scale.y : 1
        this.verticalGauge = vertical
        this.rate = parseFloat(processBindString(rate, this.itemIndex, this.passObject, this.parentWindow), 10).clamp(
            0,
            1,
        )
        this.draw()
    }

    draw() {
        const { imageName, hue } = this
        this.bitmap = ImageManager.loadSystem(imageName, hue)
        this.setFrame(
            0,
            this.verticalGauge ? (1 - this.rate) * this.bitmap.height : 0,
            this.bitmap.width * (this.verticalGauge ? 1 : this.rate),
            this.bitmap.height * (this.verticalGauge ? this.rate : 1),
        )
    }

    clear() {
        this.bitmap = ImageManager.loadEmptyBitmap()
    }
}
