import { Sprite, Graphics, Scene_Base, Scene_Boot, ImageManager, Window_Base, TilingSprite } from 'rmmv'
import { getLunaConfig, setCurrentScene } from '@/managers/LunaManager'
import { Container } from '@/components'
import { mapConfigName } from '@/utils'
import { Constants } from '@/utils/constants'

export const overrideSceneBase = () => {
    const { initialize, create, createWindowLayer, addWindow, addChild, start, stop, update } = Scene_Base.prototype

    Scene_Base.prototype.initialize = function() {
        initialize.call(this)
        this.initLuna()
    }

    Scene_Base.prototype.initLuna = function() {
        const { name } = this.constructor || ''

        this.lunaName = mapConfigName(name)
        this.lunaConfig = undefined

        this.windowNameList = []
    }

    Scene_Base.prototype.create = function() {
        this.lunaConfig = getLunaConfig(this.lunaName)
        setCurrentScene(this.lunaName)
        this.createSceneContainer()
        this.createLunaBackground()

        if (!this.lunaConfig && !(this instanceof Scene_Boot)) {
            console.warn(`[LunaEngine][Scene] ${this.lunaName} doesn't have a config.`)
        }

        create.call(this)

        this.addChild(this.sceneContainer)
    }

    Scene_Base.prototype.createSceneContainer = function() {
        this.sceneContainer = new Container()
        this.sceneContainer.sceneConfig = this.lunaConfig
        this.sceneContainer.setFrame(0, 0, Graphics.width, Graphics.height)
        this.sceneContainer.create()
    }

    Scene_Base.prototype.createLunaBackground = function() {
        const backgroundConfig = this.getLunaBackgroundConfig()

        if (!backgroundConfig) {
            console.warn(`[LunaEngine][Scene] ${this.lunaName} doesn't have background config.`)
            return
        }

        this.lunaBackgroundSprite = new Sprite()
        this.lunaBackgroundSprite.lunaName = 'Background'
        this.lunaBackgroundSprite.lunaConfig = backgroundConfig
        this.sceneContainer.addChild(this.lunaBackgroundSprite)

        if (backgroundConfig.background.type === Constants.Background.Image) {
            const isLoop = !!backgroundConfig.background.loop
            const bitmap = ImageManager.loadSystem(backgroundConfig.background.image)
            this._backgroundSprite = isLoop ? new TilingSprite(bitmap) : new Sprite(bitmap)
            if (isLoop) {
                this._backgroundSprite.move(0, 0, Graphics.width, Graphics.height)
                this._backgroundLoop = backgroundConfig.background.loop
            }
            this.lunaBackgroundSprite.addChild(this._backgroundSprite)
        }
    }

    Scene_Base.prototype.getLunaBackgroundConfig = function() {
        if (!this.lunaConfig) {
            return null
        }
        return this.lunaConfig.components ? this.lunaConfig.components.Background : null
    }

    Scene_Base.prototype.addWindow = function(window) {
        addWindow.call(this, window)
        // helper to show all windows in a scene
        this.windowNameList.push(mapConfigName(window.constructor.name))
        // setup if has configs
        if (!this.lunaConfig) {
            return
        }
        this.setupLunaWindow(window)
    }

    Scene_Base.prototype.addChild = function(child) {
        if (child instanceof Window_Base) {
            this.setupLunaWindow(child)
            return
        }
        addChild.call(this, child)
    }

    Scene_Base.prototype.start = function() {
        start.call(this)

        if (this.sceneContainer) {
            this.sceneContainer.setupLuna()
            // put scene container at the end of children list to display GUI above all spriteset
            this.addChild(this.sceneContainer)
        }
    }

    Scene_Base.prototype.stop = function() {
        stop.call(this)
        this.sceneContainer.stopLuna()
    }

    Scene_Base.prototype.setupLunaWindow = function(window) {
        this.sceneContainer.addChild(window)
    }

    Scene_Base.prototype.isAnimating = function() {
        return this.sceneContainer.isAnimating()
    }

    Scene_Base.prototype.update = function() {
        update.call(this)
        this.updateLoopBackground()
    }

    Scene_Base.prototype.updateLoopBackground = function() {
        if (!this._backgroundLoop) {
            return
        }
        this._backgroundSprite.origin.x += this._backgroundLoop.x
        this._backgroundSprite.origin.y += this._backgroundLoop.y
    }

    // returns alias
    return {
        initialize,
        create,
        createWindowLayer,
        addWindow,
        addChild,
        start,
        stop,
        update,
    }
}
