import { Bitmap } from 'rmmv'
import { SpriteLunatic } from './SpriteLunatic'
import { getPresets } from '@/managers'
import { getBindStrings, processBindString } from '@/utils/bindData'

export class SpriteText extends SpriteLunatic {
    initialize() {
        super.initialize()

        this.bitmap = new Bitmap(0, 0)
        this.text = '' // cache current text
    }

    setupParentSettings() {
        this.fontSize = this.parentWindow.standardFontSize()
        this.fontName = this.parentWindow.standardFontFace()
        this.textColor = this.parentWindow.defaultTextColor()
        this.fontOutline = this.parentWindow.fontOutline() || {
            enable: true,
            lineWidth: 4,
            lineColor: 'rgba(16, 16, 16, 1)',
        }
        this.fontShadow = this.parentWindow.fontShadow() || {
            enable: true,
            shadowOffset: {
                x: 2,
                y: 2,
            },
            shadowColor: 'rgba(32, 32, 32, 0.6)',
            shadowBlur: 4,
        }
        this.setupFont()
    }

    setupBindFields({ text }) {
        this.bindFields = getBindStrings(text || '')
        this.bindValues = this.getBindValues()
    }

    /* eslint-disable no-unused-vars */
    prepare({ text, textColor, fontName, fontSize, fontOutline, fontShadow }) {
        this.text = processBindString(text, this.itemIndex, this.passObject, this.parentWindow)
        this.fontName = this.getValue('fontName', this.lunaConfig, getPresets()) || this.fontName
        this.fontSize = this.getValue('fontSize', this.lunaConfig, getPresets()) || this.fontSize
        this.fontOutline = this.getValue('fontOutline', this.lunaConfig, getPresets()) || this.fontOutline
        this.fontShadow = this.getValue('fontShadow', this.lunaConfig, getPresets()) || this.fontShadow
        this.textColor = this.getValue('textColor', this.lunaConfig, getPresets()) || this.textColor
        this.setupFont()
        this.draw()
    }
    /* eslint-enable no-unused-vars */

    draw() {
        const { text, bitmap } = this
        const width = Math.ceil(bitmap.measureTextWidth(text) + 12)
        const height = Math.ceil(this.fontSize * 1.5)
        this.clear()
        bitmap.resize(width, height)
        bitmap.drawText(text, 6, 0, width, height)
        this.setFrame(0, 0, width, height)
    }

    clear() {
        this.bitmap.clear()
    }

    setupFont() {
        const { bitmap } = this
        bitmap.outlineEnable = this.fontOutline.enable
        bitmap.outlineColor = this.fontOutline.lineColor
        bitmap.outlineWidth = this.fontOutline.lineWidth
        bitmap.shadowEnable = this.fontShadow.enable
        bitmap.shadowOffsetX = this.fontShadow.shadowOffset.x
        bitmap.shadowOffsetY = this.fontShadow.shadowOffset.y
        bitmap.shadowColor = this.fontShadow.shadowColor
        bitmap.shadowBlur = this.fontShadow.shadowBlur
        bitmap.fontFace = this.fontName
        bitmap.fontSize = this.fontSize
        bitmap.textColor = this.textColor
    }
}
