import { Scene_ItemBase, Graphics } from 'rmmv'
import { getImprovements } from '@/managers'

export const overrideSceneItemBase = () => {
    const { initialize, showSubWindow } = Scene_ItemBase.prototype

    Scene_ItemBase.prototype.initialize = function() {
        initialize.call(this)
        this.isStaticActorWindow = !!getImprovements().StaticMenuActorWindow
    }

    Scene_ItemBase.prototype.showSubWindow = function(window) {
        if (!this.isStaticActorWindow) {
            window.x = this.isCursorLeft() ? Graphics.boxWidth - window.width : 0
        }
        window.show()
        window.activate()
    }

    // returns alias
    return {
        showSubWindow,
    }
}
