import { Utils } from 'rmmv'
import { safeLoad } from 'js-yaml'
import { getBindStrings } from '@/utils/bindData'

// base directory to contains luna configurations
const baseLunaPath = 'luna'
// contains all luna configurations
const lunaData = {}
// check if luna configs are loading
const lunaLoading = {}
// preload function for each field in configs
const lunaPreload = {}
// current scene config
let currentScene = ''

/**
 * Get local path to Luna directory
 */
const getLocalPath = () => {
    const path = __non_webpack_require__('path')
    const base = path.dirname(window.process.mainModule.filename)
    return path.join(base, baseLunaPath)
}

/**
 * Make filename on disk based on config name
 *
 * @param {string} configName
 */
const makeFilePath = configName => `${configName}.yml`

/**
 * Iterates through all components in config and preload things.
 *
 * @param {Luna.ComponentConfig} config
 */
export const preload = config => {
    if (!config) {
        return
    }

    const { components, presets } = config

    // recursive preload
    if (components) {
        const names = Object.keys(components)
        names.forEach(name => preload(components[name]))
    }

    if (presets) {
        const names = Object.keys(presets)
        names.forEach(name => preload(presets[name]))
    }

    const preloadField = Object.keys(lunaPreload)
    preloadField.forEach(field => {
        if (!config[field]) {
            return
        }
        const value = config[field]
        // binded string couldn't be preload
        if (getBindStrings(`${value}`).length > 0) {
            return
        }
        if (value !== 'default') {
            lunaPreload[field](config[field])
        }
    })
}

/**
 * Iterates through all lunatic components in config and preload things.
 *
 * @param {Luna.ComponentConfig} config
 */
export const preloadLunatic = config => {
    Object.keys(config).forEach(component => {
        const lunatic = config[component]
        preload(lunatic)
    })
}

const mergeExternalConfig = list => {
    Object.entries(list).forEach(([name, val]) => {
        if (!val.constructor || val.constructor.name !== 'String') {
            return
        }
        const [configName, entryName] = val.split('.')
        loadLunaConfig(configName, entryConfig => {
            list[name] = entryConfig[entryName]
            preload(entryConfig[entryName])
        })
    })
}

const loadExternalConfig = config => {
    if (config.components) {
        mergeExternalConfig(config.components)
    }

    if (config.lunatic) {
        mergeExternalConfig(config.lunatic)
    }
}

/**
 * Set config into manager.
 * @param {string} configName
 * @param {any} config
 */
const setLunaConfig = (configName, config) => {
    lunaData[configName] = config
    preload(lunaData[configName])
    if (lunaData[configName].lunatic) {
        preloadLunatic(lunaData[configName].lunatic)
    }
    loadExternalConfig(config)
}

/**
 * Load Luna if playing on local
 *
 * @param {string} configName
 */
const loadConfigLocal = (configName, callback) => {
    const fs = __non_webpack_require__('fs')
    const filePath = `${getLocalPath()}/${makeFilePath(configName)}`

    if (!fs.existsSync(filePath)) {
        console.error(`[LunaEngine] ${filePath} not found`)
        return
    }

    const data = fs.readFileSync(filePath, 'utf8')
    const parsedData = safeLoad(data)
    setLunaConfig(configName, parsedData)
    if (callback) {
        callback(parsedData)
    }
}

/**
 * Load Luna if playing on browser
 *
 * @param {string} configName
 */
const loadConfigBrowser = (configName, callback) => {
    const xhr = new XMLHttpRequest()
    const url = `${baseLunaPath}/${makeFilePath(configName)}`
    xhr.open('GET', url)
    xhr.overrideMimeType('application/json')
    xhr.onload = () => {
        if (xhr.status < 400) {
            const parsedData = safeLoad(xhr.responseText)
            setLunaConfig(configName, parsedData)
            if (callback) {
                callback(parsedData)
            }
        }
        delete lunaLoading[configName]
    }
    lunaLoading[configName] = true
    xhr.send()
}

/**
 * Load Luna configuration into memory
 *
 * @param {string} configName
 */
export const loadLunaConfig = (configName, callback) => {
    if (Utils.isNwjs()) {
        loadConfigLocal(configName, callback)
        return
    }
    loadConfigBrowser(configName, callback)
}

/**
 * Get Luna configuration from memory
 *
 * @param {string} configName
 * @return {Luna.ComponentConfig}
 */
export const getLunaConfig = configName => lunaData[configName]

/**
 * Check if Luna is loaded
 */
export const isLoading = () => Object.keys(lunaLoading).length > 0

/**
 * Set current scene.
 *
 * @param {string} configName
 */
export const setCurrentScene = configName => {
    currentScene = configName
}

/**
 * Get current scene config.
 *
 * @return {Luna.ComponentConfig}
 */
export const getCurrentConfig = () => getLunaConfig(currentScene) || {}

/**
 * Register preload function for given field.
 *
 * Function interface: (value) => void
 *
 * @param {string} field
 * @param {Function} preloadFunction
 */
export const registerPreload = (field, preloadFunction) => {
    const oldPreloadFunction = lunaPreload[field]

    if (!oldPreloadFunction) {
        lunaPreload[field] = preloadFunction
        return
    }

    lunaPreload[field] = value => {
        oldPreloadFunction(value)
        preloadFunction(value)
    }
}

/**
 * Get all presets.
 */
export const getPresets = () => getLunaConfig('presets').presets

/**
 * Get all animations.
 */
export const getAnimations = () => getLunaConfig('animations').animations

/**
 * Get all improvements.
 */
export const getImprovements = () => getLunaConfig('general').improvements
