import { Game_Actor } from 'rmmv'
import { getImprovements } from '@/managers/LunaManager'

export const overrideGameActor = () => {
    const { isSpriteVisible } = Game_Actor.prototype

    Game_Actor.prototype.isSpriteVisible = function() {
        return isSpriteVisible.call(this) || this.isHUDOnly()
    }

    Game_Actor.prototype.isHUDOnly = function() {
        return !!getImprovements().ShowAnimationOnHud
    }

    return {
        isSpriteVisible,
    }
}
