import { Scene_MenuBase } from 'rmmv'
import { Constants } from '@/utils/constants'

export const overrideSceneMenuBase = () => {
    const { createBackground } = Scene_MenuBase.prototype

    Scene_MenuBase.prototype.createBackground = function() {
        createBackground.call(this)
        const backgroundConfig = this.getLunaBackgroundConfig()
        if (!backgroundConfig) {
            return
        }
        const bgType = backgroundConfig.background.type
        const isDefault = bgType === Constants.Background.Default
        if (this.lunaBackgroundSprite) {
            this.lunaBackgroundSprite.addChild(this._backgroundSprite)
        }
        if (!isDefault) {
            this._backgroundSprite.visible = false
        }
    }

    // returns alias
    return {
        createBackground,
    }
}
