import { ImageManager, Window_Base, Sprite, Point, SceneManager } from 'rmmv'
import { getValue, mapConfigName } from '@/utils'
import { getPresets, getCurrentConfig } from '@/managers/LunaManager'
import { Layout } from '@/components'
import { drawLunatic } from '@/utils/drawLunatic'
import { convertBindString } from '@/utils/bindData'

export const overrideWindowBase = () => {
    const {
        _refreshContents,
        _updateContents,
        _updateCursorPosition,
        initialize,
        standardFontFace,
        standardFontSize,
        resetFontSettings,
        resetTextColor,
        lineHeight,
        textPadding,
        standardPadding,
        loadWindowskin,
        drawGauge,
        drawItemName,
        gaugeBackColor,
        addChild,
        show,
        hide,
    } = Window_Base.prototype

    Window_Base.prototype.initialize = function (x, y, width, height) {
        this.initLuna()
        initialize.call(this, x, y, width, height)
        this.setupLunatic()
    }

    Window_Base.prototype.initLuna = function () {
        const { name } = this.constructor || ''
        const { components, lunatic } = getCurrentConfig()

        this.lunaName = this.constructor.lunaName || mapConfigName(name)
        this.lunaConfig = components ? components[this.lunaName] : undefined
        this.lunaLunatic = lunatic ? lunatic[this.lunaName] : undefined
        this.bindItemList = []
        this.lunaticItemSprites = []

        if (!this.anchor) {
            this.anchor = new Point(0, 0)
        }

        if (this.lunaConfig) {
            this.initExtendLuna()
        }

        this.isLunaticMode = this.lunaLunatic ? this.lunaLunatic.enable : false
        this.isLunaticAddition = this.lunaLunatic ? !!this.lunaLunatic.additionMode : false
    }

    Window_Base.prototype.initExtendLuna = function () {
        const { components, lunatic } = getCurrentConfig()
        const { extend } = this.lunaConfig

        if (extend) {
            const extendConfig = components ? components[extend] : undefined
            const extendLunatic = lunatic ? lunatic[extend] : undefined
            this.lunaConfig = this.lunaConfig ? Object.assign({}, extendConfig, this.lunaConfig) : undefined
            if (!extendLunatic) {
                return
            }
            // TODO: deep merge
            this.lunaLunatic = Object.entries(this.lunaLunatic || {}).reduce(
                (prev, [name, val]) => ({
                    ...prev,
                    [name]: val instanceof Object ? Object.assign({}, prev[name], val) : val,
                }),
                Object.assign({}, extendLunatic),
            )
        }
    }

    Window_Base.prototype.setupLunatic = function () {
        if (!this.isLunaticMode) {
            return
        }
        const { contents, itemList } = this.lunaLunatic
        const lunaName = `${this.lunaName}LunaticLayout`
        const lunaConfig = { type: contents.type }
        this.contentsLayout = new Layout()
        this.contentsLayout.lunaName = lunaName
        this.contentsLayout.lunaConfig = lunaConfig
        this.addChild(this.contentsLayout)

        this.bindItemList = itemList ? convertBindString(`\${${itemList}}`) : []
        if (!Array.isArray(this.bindItemList)) {
            throw new Error(`[Lunatic] Item list ${itemList} for component ${this.lunaName} is not an array`)
        }

        if (this.setItem) {
            const _setItem = this.setItem
            this._oldItem = null
            this.setItem = item => {
                _setItem.call(this, item)
                Window_Base.onSetItem.call(this, item)
            }
        }

        if (this.refresh) {
            const _refresh = this.refresh
            this.refresh = () => {
                if (!this.isSetupDone && this.lunaConfig) {
                    return
                }
                if (!this.isLunaticMode) {
                    _refresh.call(this)
                    return
                }
                if (this.isLunaticAddition) {
                    _refresh.call(this)
                }
                if (this.makeItemList) {
                    this.makeItemList()
                }
                this.refreshLunatic()
            }
            return
        }

        this.refreshLunatic()
    }

    Window_Base.onSetItem = function (item) {
        if (this._oldItem !== item) {
            this._oldItem = item
            this.refreshLunatic()
        }
    }

    Window_Base.prototype.setupLunaticItems = function () {
        // setup in Window Selectable
    }

    Window_Base.prototype.refreshLunatic = function () {
        this.clearLunatic()
        this.drawLunaticContents()
        this.drawLunaticItems()

        if (this.contentsLayout) {
            this.contentsLayout.setupLuna()
        }

        if (this.ensureCursorVisible) {
            this.ensureCursorVisible()
        }
    }

    Window_Base.prototype.clearLunatic = function () {
        this.contentsLayout.children = []
    }

    Window_Base.prototype.drawLunaticContents = function () {
        const contentComponents = this.lunaLunatic.contents.components
        const lunaticComponents = this.lunaLunatic.components
        contentComponents.forEach(componentName => {
            const componentConfig = lunaticComponents[componentName]
            const sprite = drawLunatic(componentConfig)
            sprite.setParentWindow(this)
            if (this._oldItem) {
                sprite.setpassObject(this._oldItem)
            }
            sprite.setup(componentConfig)
            sprite.lunaConfig = componentConfig
            sprite.lunaName = `${this.lunaName}${componentName}`
            this.contentsSprite = sprite
            this.contentsLayout.addChild(sprite)
        })
    }

    Window_Base.prototype.drawLunaticItems = function () {
        // to be defined in window selectable
    }

    Window_Base.prototype._refreshContents = function () {
        _refreshContents.call(this)
        const offset = this.contentsOffset()
        if (!offset) {
            return
        }
        this._windowContentsSprite.x += offset.x
        this._windowContentsSprite.y += offset.y
    }

    Window_Base.prototype._updateCursorPosition = function () {
        _updateCursorPosition.call(this)
        const offset = this.contentsOffset()
        if (!offset) {
            return
        }
        this._windowCursorSprite.x += offset.x
        this._windowCursorSprite.y += offset.y
    }

    Window_Base.prototype._updateContents = function () {
        _updateContents.call(this)
        if (!this.isLunaticMode) {
            return
        }
        if (!this.isLunaticAddition) {
            this._windowContentsSprite.visible = false
        }
        this.contentsLayout.visible = this.visible && this.isOpen()
    }

    Window_Base.prototype.resetFontSettings = function () {
        resetFontSettings.call(this)

        if (!this.lunaConfig) {
            return
        }

        // outline
        const fontOutline = this.fontOutline()
        this.contents.outlineEnable = fontOutline.enable
        this.contents.outlineColor = fontOutline.lineColor
        this.contents.outlineWidth = fontOutline.lineWidth

        // shadow
        const fontShadow = this.fontShadow()
        this.contents.shadowEnable = fontShadow.enable
        this.contents.shadowOffsetX = fontShadow.shadowOffset.x
        this.contents.shadowOffsetY = fontShadow.shadowOffset.y
        this.contents.shadowColor = fontShadow.shadowColor
        this.contents.shadowBlur = fontShadow.shadowBlur
    }

    Window_Base.prototype.resetTextColor = function () {
        this.changeTextColor(this.defaultTextColor())
    }

    Window_Base.prototype.loadWindowskin = function () {
        if (!this.lunaConfig) {
            loadWindowskin.call(this)
            return
        }
        const windowSkin = getValue('windowSkin', this.lunaConfig, getPresets())
        this.windowskin = ImageManager.loadSystem(windowSkin)
    }

    /**
     * Fixed draw item name doesn't follow textPadding
     */
    Window_Base.prototype.drawItemName = function (item, x, y, width = 312) {
        if (item) {
            const iconBoxWidth = Window_Base._iconWidth + 4
            this.resetTextColor()
            this.drawIcon(item.iconIndex, x + 2 + this.textPadding(), y + 2)
            this.drawText(item.name, x + iconBoxWidth + this.textPadding(), y, width - iconBoxWidth)
        }
    }

    Window_Base.prototype.drawGauge = function (dx, dy, dw, rate, color1, color2) {
        if (!this.lunaConfig) {
            drawGauge.call(this, dx, dy, dw, rate, color1, color2)
            return
        }

        const gaugeOutline = this.gaugeOutline()
        const gaugeOffset = getValue('gaugeOffset', this.lunaConfig, getPresets())

        this.drawLunaGauge({
            dx,
            dy,
            dw,
            dh: this.gaugeHeight(),
            rate,
            color1,
            color2,
            gaugeOutline,
            gaugeOffset,
            rectHeight: this.lineHeight(),
            slanted: this.gaugeSlanted(),
            gaugeBackColor: this.gaugeBackColor(),
        })
    }

    Window_Base.prototype.drawLunaGauge = function ({
        dx,
        dy,
        dw,
        dh,
        rate,
        color1,
        color2,
        gaugeOutline,
        gaugeOffset,
        rectHeight,
        slanted,
        gaugeBackColor,
    }) {
        const outlineColor = gaugeOutline.lineColor
        const outlineWidth = gaugeOutline.lineWidth

        let fillW = Math.floor(dw * rate).clamp(0, dw)
        let gaugeH = dh
        let gaugeX = dx + (gaugeOffset ? gaugeOffset.x : 0)
        let gaugeY = dy + (rectHeight - gaugeH) / 2 + (gaugeOffset ? gaugeOffset.y : 0)

        if (gaugeOutline.enable) {
            if (slanted) {
                this.fillSlantedRect(gaugeX, gaugeY, dw, gaugeH, outlineColor)
            } else {
                this.contents.fillRect(gaugeX, gaugeY, dw, gaugeH, outlineColor)
            }
            fillW = Math.max(fillW - outlineWidth * 2, 0)
            gaugeH -= outlineWidth * 2
            gaugeX += outlineWidth
            gaugeY += outlineWidth
        }

        if (slanted) {
            this.fillSlantedRect(gaugeX, gaugeY, dw - outlineWidth * 2, gaugeH, gaugeBackColor)
            this.gradientFillSlantedRect(gaugeX, gaugeY, fillW, gaugeH, color1, color2)
        } else {
            this.contents.fillRect(gaugeX, gaugeY, dw - outlineWidth * 2, gaugeH, gaugeBackColor)
            this.contents.gradientFillRect(gaugeX, gaugeY, fillW, gaugeH, color1, color2)
        }
    }

    Window_Base.prototype.fillSlantedRect = function (x, y, w, h, color) {
        const offX = Math.round(h / 2)
        for (let l = 0; l < h; l++) {
            this.contents.fillRect(x + (offX - l), y + l, w, 1, color)
        }
    }

    Window_Base.prototype.gradientFillSlantedRect = function (x, y, w, h, color1, color2) {
        const offX = Math.round(h / 2)
        for (let l = 0; l < h; l++) {
            this.contents.gradientFillRect(x + (offX - l), y + l, w, 1, color1, color2)
        }
    }

    Window_Base.prototype.setupLuna = function () {
        if (!this.lunaConfig) {
            return
        }
        this.isSetupDone = true
        this.setupBackground()
        this.setupCursor()
        if (this.refresh) {
            this.refresh()
        }
        if (this.contentsLayout) {
            this.contentsLayout.setupLuna()
        }
    }

    Window_Base.prototype.setupBackground = function () {
        const customBackground = getValue('customBackground', this.lunaConfig, getPresets())
        const { enable, image, offset } = customBackground

        if (!enable) {
            return
        }

        if (this.customBackgroundSprite) {
            return
        }

        const bitmap = image.toLowerCase() === 'none' ? ImageManager.loadEmptyBitmap() : ImageManager.loadSystem(image)
        this.customBackgroundSprite = new Sprite(bitmap)
        if (offset) {
            this.customBackgroundSprite.x = offset.x
            this.customBackgroundSprite.y = offset.y
        }
        // no need the windowskin background
        this._windowSpriteContainer.removeChild(this._windowBackSprite)
        this._windowSpriteContainer.removeChild(this._windowFrameSprite)
        this._windowSpriteContainer.addChild(this.customBackgroundSprite)
    }

    Window_Base.prototype.setupCursor = function () {
        const setting = this.cursorSetting()

        this.enableCursor = setting.type !== 'none'
        this.cursorBlink = setting.blink
        this.cursorOffset = setting.offset
        this.customCursor.enable = setting.type === 'image'
        this.customCursor.image = setting.image
    }

    Window_Base.prototype.resizeMove = function (x, y, width, height) {
        const realWidth = width === undefined ? this.width : width
        const realHeight = height === undefined ? this.height : height
        const realX = x - realWidth * this.anchor.x
        const realY = y - realHeight * this.anchor.y

        const needRefresh = realWidth !== this.width || realHeight !== this.height

        this.x = realX
        this.y = realY
        this.width = realWidth
        this.height = realHeight

        if (this.contentsLayout) {
            const padding = this.standardPadding()
            this.contentsLayout.resizeMove(padding, padding, this.width - padding * 2, this.height - padding * 2)
            this.contentsLayout.setupLuna()
        }

        if (!needRefresh) {
            return
        }

        this._refreshAllParts()
        this.createContents()

        if (this.refresh) {
            this.refresh()
        }

        if (this.updateCursor) {
            this.updateCursor()
        }

        if (this.setTopRow && this.topRow) {
            this.setTopRow(this.topRow())
        }
    }

    Window_Base.prototype.setAnchor = function (x, y) {
        this.anchor.x = x
        this.anchor.y = y
    }

    Window_Base.prototype.addChild = function (child) {
        addChild.call(this, child)
        if (child) {
            SceneManager.getContainer().addNamedChild(child)
        }
    }

    Window_Base.prototype.show = function () {
        show.call(this)
        if (this.sceneContainer) {
            this.sceneContainer.onComponentShowEvents(this)
        }
    }

    Window_Base.prototype.hide = function () {
        if (!this.doneHiddenAnimation && this.sceneContainer) {
            this.sceneContainer.onComponentHideEvents(this)
        }
        if (!this.hideAfterAnimations) {
            hide.call(this)
            this.doneHiddenAnimation = false
        }
    }

    /**
     *
     * Config part
     *
     */

    Window_Base.prototype.standardFontFace = function () {
        if (!this.lunaConfig) {
            return standardFontFace.call(this)
        }
        return getValue('fontName', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.standardFontSize = function () {
        if (!this.lunaConfig) {
            return standardFontSize.call(this)
        }
        return getValue('fontSize', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.defaultTextColor = function () {
        if (!this.lunaConfig) {
            return this.normalColor.call(this)
        }
        return getValue('textColor', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.fontOutline = function () {
        if (!this.lunaConfig) {
            return null
        }
        return getValue('fontOutline', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.fontShadow = function () {
        if (!this.lunaConfig) {
            return null
        }
        return getValue('fontShadow', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.lineHeight = function () {
        if (!this.lunaConfig) {
            return lineHeight.call(this)
        }
        return getValue('lineHeight', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.textPadding = function () {
        if (!this.lunaConfig) {
            return textPadding.call(this)
        }
        return getValue('textPadding', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.standardPadding = function () {
        if (!this.lunaConfig) {
            return standardPadding.call(this)
        }
        return getValue('windowPadding', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.gaugeOutline = function () {
        if (!this.lunaConfig) {
            return null
        }
        return getValue('gaugeOutline', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.gaugeBackColor = function () {
        if (!this.lunaConfig) {
            return gaugeBackColor.call(this)
        }
        return getValue('gaugeBackColor', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.gaugeHeight = function () {
        if (!this.lunaConfig) {
            return standardPadding.call(this)
        }
        return getValue('gaugeHeight', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.gaugeSlanted = function () {
        if (!this.lunaConfig) {
            return false
        }
        return getValue('gaugeSlanted', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.cursorSetting = function () {
        if (!this.lunaConfig) {
            return false
        }
        return getValue('cursor', this.lunaConfig, getPresets())
    }

    Window_Base.prototype.contentsOffset = function () {
        if (!this.lunaConfig) {
            return false
        }
        return getValue('contentsOffset', this.lunaConfig, getPresets())
    }

    return {
        _refreshContents,
        _updateContents,
        _updateCursorPosition,
        initialize,
        standardFontFace,
        standardFontSize,
        resetFontSettings,
        resetTextColor,
        lineHeight,
        textPadding,
        standardPadding,
        loadWindowskin,
        drawGauge,
        drawItemName,
        addChild,
    }
}
