import { SpriteImage } from '@/sprites/SpriteImage'
// FIXME: Add an abstract layer to remove cicular import
/* eslint-disable import/no-cycle */
import { SpriteGrid } from '@/sprites/SpriteGrid'
/* eslint-enable import/no-cycle */
import { SpriteFace } from '@/sprites/SpriteFace'
import { SpriteText } from '@/sprites/SpriteText'
import { SpriteGauge } from '@/sprites/SpriteGauge'
import { SpriteIcon } from '@/sprites/SpriteIcon'
import { SpriteGaugeImage } from '@/sprites/SpriteGaugeImage'

const newSprite = config => {
    switch (config.type.toLowerCase()) {
        case 'image':
            return new SpriteImage()
        case 'text':
            return new SpriteText()
        case 'gauge':
            return new SpriteGauge()
        case 'imagegauge':
            return new SpriteGaugeImage()
        case 'grid':
            return new SpriteGrid()
        case 'face':
            return new SpriteFace()
        case 'icon':
            return new SpriteIcon()
        default:
            throw new Error(`Lunatic type ${config.type} is not supported`)
    }
}

export const drawLunatic = config => {
    const sprite = newSprite(config)
    return sprite
}
