import { Graphics } from 'rmmv'

export const overrideGraphics = () => {
    Graphics.loadFontExtended = function(name) {
        const style = document.createElement('style')
        const head = document.getElementsByTagName('head')
        const projectDirectory = window.location.pathname.substring(0, window.location.pathname.lastIndexOf('/'))
        const baseUrl = `${projectDirectory}/fonts/${name}`
        const rule = `@font-face {
            font-family: "${name}";
            src: url("${baseUrl}.ttf") format("truetype"),
                 url("${baseUrl}.woff2") format("woff2"),
                 url("${baseUrl}.woff") format("woff"),
                 url("${baseUrl}.otf") format("opentype");
        }`
        style.type = 'text/css'
        head.item(0).appendChild(style)
        style.sheet.insertRule(rule, 0)
        /* eslint-disable no-underscore-dangle */
        this._createFontLoader(name)
    }

    return {}
}
