import { Sprite, SceneManager } from 'rmmv'
import { mapConfigName } from '@/utils'

export const overrideSprite = () => {
    const { initialize, addChild, removeChild } = Sprite.prototype

    Sprite.prototype.initialize = function(bitmap) {
        initialize.call(this, bitmap)
        this.initLuna()
    }

    Sprite.prototype.initLuna = function() {
        const { name } = this.constructor || ''

        this.lunaName = mapConfigName(name)
        this.lunaConfig = undefined
    }

    Sprite.prototype.setupLuna = function() {}

    Sprite.prototype.resizeMove = function(x, y, width, height) {
        this.move(x, y)
        // FIXME: this is set frame
        if (width && height) {
            this.width = width
            this.height = height
        }
    }

    Sprite.prototype.setAnchor = function(x, y) {
        this.anchor.x = x.clamp(0, 1)
        this.anchor.y = y.clamp(0, 1)
    }

    Sprite.prototype.addChild = function(child) {
        addChild.call(this, child)
        if (child) {
            SceneManager.getContainer().addNamedChild(child)
        }
    }

    Sprite.prototype.removeChild = function(child) {
        removeChild.call(this, child)
        if (child) {
            SceneManager.getContainer().removeNamedChild(child)
        }
    }

    return { initialize, addChild }
}
