import { Graphics, Sprite_Battler } from 'rmmv'

export const compatibleYEPXVisualHpGauge = () => {
    const { Window_VisualHPGauge, Imported } = window

    if (!Imported || !Imported.YEP_X_VisualHpGauge) {
        return {}
    }

    const _Window_VisualHPGauge_makeWindowBoundaries = Window_VisualHPGauge.prototype.makeWindowBoundaries
    Window_VisualHPGauge.prototype.makeWindowBoundaries = function() {
        if (!this._requestRefresh) return
        this._minX = -1 * this.standardPadding()
        this._maxX = Graphics.boxWidth - this.width + this.standardPadding()
        this._minY = -1 * this.standardPadding()
        this._maxY = Graphics.boxHeight - this.height + this.standardPadding()
    }

    const _Window_VisualHPGauge_updateWindowPosition = Window_VisualHPGauge.prototype.updateWindowPosition
    Window_VisualHPGauge.prototype.updateWindowPosition = function() {
        _Window_VisualHPGauge_updateWindowPosition.call(this)
        // this.x += this.lunaConfig.offset.x
        // this.y += this.lunaConfig.offset.y
        if (!this.customBackgroundSprite) {
            return
        }
        this.customBackgroundSprite.x = (this.width - this.customBackgroundSprite.width) / 2
        this.customBackgroundSprite.y = (this.height - this.customBackgroundSprite.height) / 2
    }

    const _Sprite_Battler_createVisualHpGaugeWindow = Sprite_Battler.prototype.createVisualHpGaugeWindow
    Sprite_Battler.prototype.createVisualHpGaugeWindow = function() {
        _Sprite_Battler_createVisualHpGaugeWindow.call(this)
        if (this._visualHpGauge && !this._visualHpGauge.isLunaSetup) {
            this._visualHpGauge.setupLuna()
            this._visualHpGauge.isLunaSetup = true
        }
    }

    // returns alias
    return {
        _Window_VisualHPGauge_makeWindowBoundaries,
        _Window_VisualHPGauge_updateWindowPosition,
        _Sprite_Battler_createVisualHpGaugeWindow,
    }
}
