import { Scene_Battle } from 'rmmv'
import { getImprovements } from '@/managers'

export const overrideSceneBattle = () => {
    const { selectActorSelection, onActorOk, onActorCancel, updateWindowPositions } = Scene_Battle.prototype

    Scene_Battle.prototype.selectActorSelection = function() {
        selectActorSelection.call(this)

        if (getImprovements().HideBattleStatusOnSelectActor) {
            this._statusWindow.hide()
        }
    }

    Scene_Battle.prototype.onActorOk = function() {
        onActorOk.call(this)
        this._statusWindow.show()
    }

    Scene_Battle.prototype.onActorCancel = function() {
        onActorCancel.call(this)
        this._statusWindow.show()
    }

    Scene_Battle.prototype.updateWindowPositions = function() {
        // Luna Engine will handle this (soon)
    }

    return {
        selectActorSelection,
        onActorOk,
        onActorCancel,
        updateWindowPositions,
    }
}
