import { Window_Base, Bitmap } from 'rmmv'
import { SpriteLunatic } from './SpriteLunatic'
import { getPresets } from '@/managers'
import { getBindStrings, processBindString } from '@/utils/bindData'

export class SpriteGauge extends SpriteLunatic {
    initialize() {
        super.initialize()

        this.bitmap = new Bitmap(0, 0)
        this.rate = 1
        this.gaugeWidth = 0
        this.gaugeHeight = 0
        this.drawWindow = new Window_Base(0, 0, 0, 0)
    }

    setupParentSettings() {
        this.color1 = this.drawWindow.hpGaugeColor1()
        this.color2 = this.drawWindow.hpGaugeColor2()
        this.gaugeOutline = this.parentWindow.gaugeOutline()
        this.gaugeBackColor = this.parentWindow.gaugeBackColor()
        this.slanted = this.parentWindow.gaugeSlanted()
    }

    setupBindFields({ rate }) {
        this.bindFields = getBindStrings(rate || '')
        this.bindValues = this.getBindValues()
    }

    /* eslint-disable no-unused-vars */
    prepare({ width, height, gaugeColor1, gaugeColor2, gaugeOutline, gaugeBackColor, slanted, rate }) {
        this.slanted = !!slanted
        this.color1 = this.getValue('gaugeColor1', this.lunaConfig, getPresets()) || this.color1
        this.color2 = this.getValue('gaugeColor2', this.lunaConfig, getPresets()) || this.color2
        this.gaugeOutline = this.getValue('gaugeOutline', this.lunaConfig, getPresets()) || this.gaugeOutline
        this.gaugeBackColor = this.getValue('gaugeBackColor', this.lunaConfig, getPresets()) || this.gaugeBackColor
        this.rate = parseFloat(processBindString(rate, this.itemIndex, this.passObject, this.parentWindow), 10).clamp(
            0,
            1,
        )
        this.draw()
    }
    /* eslint-enable no-unused-vars */

    // FIXME: separate back gauge and actual gauge for performance rate changes
    draw() {
        if (!+this.gaugeWidth || !+this.gaugeHeight) {
            return
        }
        this.clear()
        if (this.gaugeWidth !== this.bitmap.width || this.gaugeHeight !== this.bitmap.height) {
            this.bitmap = new Bitmap(this.gaugeWidth + (this.slanted ? this.gaugeHeight : 0), this.gaugeHeight)
        }
        this.drawWindow.contents = this.bitmap
        this.drawWindow.drawLunaGauge({
            dx: this.slanted ? Math.floor(this.gaugeHeight / 2) : 0,
            dy: 0,
            dw: this.gaugeWidth,
            dh: this.gaugeHeight,
            rate: this.rate,
            color1: this.color1,
            color2: this.color2,
            gaugeOutline: this.gaugeOutline,
            gaugeOffset: null,
            rectHeight: this.gaugeHeight,
            slanted: this.slanted,
            gaugeBackColor: this.gaugeBackColor,
        })
        this.setFrame(0, 0, this.gaugeWidth, this.gaugeHeight)
    }

    clear() {
        this.bitmap.clear()
    }

    resizeMove(x, y, width, height) {
        super.resizeMove(x, y, width, height)

        if (width !== this.gaugeWidth || height !== this.gaugeHeight) {
            this.gaugeWidth = width
            this.gaugeHeight = height
            this.draw()
        }
    }
}
