import { Graphics, ImageManager } from 'rmmv'
import { registerPreload } from '@/managers/LunaManager'
import { getBindStrings } from './utils/bindData'

export const registerDefaultPreload = () => {
    registerPreload('fontName', value => {
        Graphics.loadFontExtended(value)
    })

    registerPreload('windowSkin', value => {
        ImageManager.reserveSystem(value)
    })

    registerPreload('image', value => {
        ImageManager.reserveSystem(value)
    })

    registerPreload('customBackground', value => {
        if (value.enable && !getBindStrings(value.image) && value.image.toLowerCase() !== 'none') {
            ImageManager.reserveSystem(value.image)
        }
    })

    registerPreload('cursor', value => {
        if (value.type === 'image' && !getBindStrings(value.image)) {
            ImageManager.reserveSystem(value.image)
        }
    })

    registerPreload('background', value => {
        if (value.type === 'image' && !getBindStrings(value.image)) {
            ImageManager.reserveSystem(value.image)
        }
    })

    registerPreload('imageName', value => {
        ImageManager.reserveSystem(value)
    })

    registerPreload('faceName', value => {
        ImageManager.reserveFace(value)
    })
}

