import {
    Bitmap,
    Sprite_Base,
    Game_Actor,
    $dataAnimations,
    SceneManager,
    Window_BattleStatus,
    Window_BattleActor,
} from 'rmmv'
import { Layout } from '@/components'
import { getValue } from '@/utils'
import { convertBindString } from '@/utils/bindData'
import { isArrayEqual } from '@/utils/isArrayEqual'
import { getRealWorldPosition } from '@/utils/getRealWorldPosition'

/**
 * SpriteLunatic
 *
 * Base sprite for lunatic draw.
 */
export class SpriteLunatic extends Sprite_Base {
    initialize() {
        super.initialize()

        this.bitmap = new Bitmap(0, 0)
        this.itemIndex = 0
        this.passObject = null
        this.lunaConfig = null
        this.lunaName = ''
        this.lunaOriginalName = ''
        this.originalConfig = null
        this.parentWindow = null
        this.isSelect = false
        this.showGameAnimation = false
        this.isBind = false
        this.spriteActor = null

        this.bindFields = []
        this.bindValues = []
        this.conditions = {}
        this.conditionsFunc = {}
    }

    setParentWindow(window) {
        this.parentWindow = window
        this.setupParentSettings()
    }

    setupParentSettings() {
        // behaviour
    }

    setItemIndex(index) {
        this.itemIndex = index
    }

    setpassObject(obj) {
        this.passObject = obj
    }

    setup(stuffs) {
        this.lunaConfig = stuffs
        this.originalConfig = stuffs
        this.setupCore(stuffs)
        this.setupBindFields(stuffs)
        this.setupConditionsFunc(stuffs)
        this.prepare(stuffs)
        this.setupBasic(stuffs)
    }

    setupCore({ bind }) {
        this.isBind = !!bind
    }

    /* eslint-disable no-unused-vars */
    setupBindFields(stuffs) {
        // behaviour
    }
    /* eslint-enable no-unused-vars */

    setupConditionsFunc({ conditions }) {
        if (!conditions) {
            return
        }
        /* eslint-disable no-new-func */
        this.conditionsFunc = Object.keys(conditions).reduce(
            (prev, condName) => ({
                ...prev,
                [condName]: new Function(
                    'data',
                    'isSelect',
                    'parentIndex',
                    'itemIndex',
                    'parent',
                    `return ${conditions[condName].condition}`,
                ),
            }),
            {},
        )
        /* eslint-enable no-new-func */
    }

    /* eslint-disable no-unused-vars */
    prepare(stuffs) {
        // behaviour
    }
    /* eslint-enable no-unused-vars */

    setupBasic({ anchor, pivot, rotation }) {
        this.showGameAnimation =
            this.passObject instanceof Game_Actor &&
            this.parentWindow.lunaConfig.showAnimationOn === this.lunaOriginalName &&
            this.parentWindow instanceof Window_BattleStatus &&
            !(this.parentWindow instanceof Window_BattleActor)

        if (this.showGameAnimation) {
            this.spriteActor = SceneManager.getContainer().namedChildren[`ActorSprite${this.passObject.index()}`]
            this.parentWindow.addChild(this.spriteActor)
        }

        this.anchor = anchor || this.anchor
        if (pivot) {
            this.pivot.set(this.width * pivot.x, this.height * pivot.y)
        }
        if (!Math.abs(rotation)) {
            return
        }
        this.rotation = (rotation * Math.PI) / 180
    }

    draw() {
        // behaviour
    }

    clear() {
        this.bitmap.clear()
    }

    refresh() {
        this.draw()
    }

    update() {
        super.update()
        this.updateConditions()
        // this.updateGameAnimations()
        this.updateBindValues()
    }

    updateVisibility() {
        super.updateVisibility()
        const { visible } = this.lunaConfig
        this.visible = visible !== undefined ? !!visible : this.visible
    }

    updateConditions() {
        let needUpdate = false

        Object.entries(this.conditionsFunc).forEach(([condName, condFunc]) => {
            const parentIndex = this.parentWindow.index ? this.parentWindow.index() : 0
            const result = !!condFunc(
                this.passObject,
                this.isSelect || this.parent.isSelect,
                parentIndex,
                this.itemIndex,
                this.parentWindow,
            )
            if (result === this.conditions[condName]) {
                return
            }
            this.conditions[condName] = result
            needUpdate = true
        })

        if (!needUpdate) {
            return
        }

        const { conditions } = this.lunaConfig
        const updateConfigs = Object.entries(this.conditions).map(
            ([condName, result]) => (result ? conditions[condName].config : {}),
        )
        this.lunaConfig = Object.assign({}, this.originalConfig, ...updateConfigs)

        if (this.parent instanceof Layout) {
            this.parent.setupLayout()
        }
        this.prepare(this.lunaConfig)
        this.updateVisibility()
    }

    updateBindValues() {
        if (!this.isBind) {
            return
        }

        const changedField = this.bindFields.find((bind, index) => {
            const converted = convertBindString(bind, this.itemIndex, this.passObject, this.parentWindow)
            if (converted instanceof Array) {
                return !isArrayEqual(converted, this.bindValues[index])
            }
            return converted !== this.bindValues[index]
        })

        if (!changedField) {
            return
        }

        this.bindValues = this.getBindValues()
        this.prepare(this.lunaConfig)
    }

    updateGameAnimations() {
        if (!this.showGameAnimation) {
            return
        }
        while (this.passObject.isAnimationRequested()) {
            const data = this.passObject.shiftAnimation()
            const animation = $dataAnimations[data.animationId]
            const { mirror } = data
            const delay = animation.position === 3 ? 0 : data.delay
            this.startAnimation(animation, mirror, delay)
        }
    }

    fittingHeight(numLines) {
        return this.parentWindow.fittingHeight(numLines)
    }

    lineHeight() {
        return this.parentWindow.lineHeight()
    }

    getValue(key, config, presets) {
        const keyPath = key.split('.')
        const configValue = keyPath.reduce((prev, cur) => prev[cur], config)
        if (configValue === undefined) {
            return null
        }
        return getValue(key, config, presets)
    }

    getBindValues() {
        return this.bindFields.map(bind => convertBindString(bind, this.itemIndex, this.passObject, this.parentWindow))
    }

    resizeMove(x, y, width, height) {
        super.resizeMove(x, y, width, height)
        if (this.showGameAnimation) {
            const parentPosition = getRealWorldPosition(this.parent)
            const parentWindowPosition = getRealWorldPosition(this.parentWindow)
            const sx =
                (this.width - this.spriteActor.width) / 2 +
                this.x +
                this.spriteActor.width * this.spriteActor.anchor.x -
                this.width * this.anchor.x +
                parentPosition.x -
                parentWindowPosition.x
            const sy =
                (this.height - this.spriteActor.height) / 2 +
                this.y +
                this.spriteActor.height * this.spriteActor.anchor.y -
                this.height * this.anchor.y +
                parentPosition.y -
                parentWindowPosition.y
            this.spriteActor.setHome(sx, sy)
        }
    }

    _onBitmapLoad(bitmapLoaded) {
        super._onBitmapLoad(bitmapLoaded)
        if (this.lunaConfig && window._disableAsync) {
            this.draw()
        }
    }
}
