import { DataManager } from 'rmmv'
import { getSaveData } from '@/utils/bindData'

export const overrideDataManager = () => {
    const { saveGame } = DataManager

    DataManager.saveGame = savefileId => {
        const result = saveGame.call(DataManager, savefileId)
        getSaveData()
        return result
    }

    return { saveGame }
}
