import { Bitmap } from 'rmmv'

export const overrideBitmap = () => {
    const { initialize, drawText, _drawTextOutline } = Bitmap.prototype

    Bitmap.prototype.initialize = function(width, height) {
        initialize.call(this, width, height)
        // new properties
        this.outlineEnable = true
        this.shadowEnable = false
        this.shadowOffsetX = 0
        this.shadowOffsetY = 0
        this.shadowColor = 'transparent'
        this.shadowBlur = 0
    }

    Bitmap.prototype.drawText = function(text, x, y, maxWidth, lineHeight, align) {
        if (this.shadowEnable) {
            const context = this._context
            context.shadowOffsetX = this.shadowOffsetX
            context.shadowOffsetY = this.shadowOffsetY
            context.shadowColor = this.shadowColor
            context.shadowBlur = this.shadowBlur
        }
        drawText.call(this, text, x, y, maxWidth, lineHeight, align)
        this.setNoShadow()
    }

    Bitmap.prototype._drawTextOutline = function(text, tx, ty, maxWidth) {
        if (this.outlineEnable) {
            _drawTextOutline.call(this, text, tx, ty, maxWidth)
        }
    }

    Bitmap.prototype.setNoShadow = function() {
        const context = this._context
        context.shadowOffsetX = 0
        context.shadowOffsetY = 0
        context.shadowColor = 'transparent'
        context.shadowBlur = 0
    }

    return {
        _drawTextOutline,
        drawText,
        initialize,
    }
}
