import { ImageManager, Window_Base } from 'rmmv'
import { SpriteLunatic } from './SpriteLunatic'
import { processBindString } from '@/utils/bindData'

export class SpriteFace extends SpriteLunatic {
    initialize() {
        super.initialize()

        this.faceName = ''
        this.faceIndex = 0
        this.hue = 0
        this.bitmap = ImageManager.loadEmptyBitmap()
    }

    prepare({ faceName, faceIndex, hue, scale }) {
        this.faceName = processBindString(faceName, this.itemIndex, this.passObject, this.parentWindow)
        this.faceIndex = parseInt(processBindString(faceIndex, this.itemIndex, this.passObject, this.parentWindow), 10)
        this.hue = hue || 0
        this.scale.x = scale ? scale.x : 1
        this.scale.y = scale ? scale.y : 1
        this.draw()
    }

    draw() {
        const { faceName, faceIndex, hue } = this
        this.bitmap = ImageManager.loadFace(faceName, hue)
        const sw = Window_Base._faceWidth
        const sh = Window_Base._faceHeight
        const sx = (faceIndex % 4) * sw
        const sy = Math.floor(faceIndex / 4) * sh
        this.setFrame(sx, sy, sw, sh)
    }

    clear() {
        this.bitmap = ImageManager.loadEmptyBitmap()
    }
}
