import { Scene_Title } from 'rmmv'
import { Constants } from '@/utils/constants'

export const overrideSceneTitle = () => {
    const { createBackground, createForeground } = Scene_Title.prototype

    Scene_Title.prototype.createBackground = function() {
        const backgroundConfig = this.getLunaBackgroundConfig()
        const bgType = backgroundConfig.background.type
        const isDefault = bgType === Constants.Background.Default
        createBackground.call(this)
        if (this.lunaBackgroundSprite) {
            this.lunaBackgroundSprite.addChild(this._backSprite1)
            this.lunaBackgroundSprite.addChild(this._backSprite2)
        }
        if (!isDefault) {
            this._backSprite1.visible = false
            this._backSprite2.visible = false
        }
    }

    Scene_Title.prototype.createForeground = function() {
        const backgroundConfig = this.getLunaBackgroundConfig()
        const bgType = backgroundConfig.background.type
        const isDefault = bgType === Constants.Background.Default
        createForeground.call(this)
        if (this.lunaBackgroundSprite) {
            this.lunaBackgroundSprite.addChild(this._gameTitleSprite)
        }
        if (!isDefault) {
            this._gameTitleSprite.visible = false
        }
    }

    // returns alias
    return {
        createBackground,
        createForeground,
    }
}
