import { Window_NumberInput } from 'rmmv'

export const overrideWindowNumberInput = () => {
    const { initialize, maxCols, maxItems } = Window_NumberInput.prototype

    Window_NumberInput.prototype.initialize = function(messageWindow) {
        initialize.call(this, messageWindow)
        this.maxCols = Window_NumberInput.prototype.maxCols.bind(this)
    }

    Window_NumberInput.prototype.maxCols = function() {
        return maxCols.call(this) || 1
    }

    Window_NumberInput.prototype.maxItems = function() {
        return maxItems.call(this) || 1
    }

    return { initialize, maxCols, maxItems }
}
