import { Graphics, Scene_Status } from 'rmmv'
import { Window_StatusBasic } from '@/windows/WindowStatusBasic'

export const overrideSceneStatus = () => {
    const { createStatusWindow } = Scene_Status.prototype

    Scene_Status.prototype.createStatusWindow = function() {
        const wx = this._commandWindow.width
        const wy = this._helpWindow.height
        const ww = Graphics.boxWidth - wx
        const wh = this._commandWindow.height
        this._statusWindow = new Window_StatusBasic(wx, wy, ww, wh)
        this.addWindow(this._statusWindow)
    }

    // returns alias
    return {
        createStatusWindow,
    }
}
