import { Window_Selectable } from 'rmmv'
import { SpriteLunatic } from './SpriteLunatic'
import { getBindStrings } from '@/utils/bindData'
import { Layout } from '@/components'
import { defaultGridLayout } from '@/utils/defaultConfig'
// FIXME: Add an abstract layer to remove cicular import
/* eslint-disable import/no-cycle */
import { drawLunatic } from '@/utils/drawLunatic'
/* eslint-enable import/no-cycle */

export class SpriteGrid extends SpriteLunatic {
    initialize() {
        super.initialize()

        this.itemList = []
        this.components = []
        this.currentIndex = -1

        this.isLunaticContents = false
        this.maxPageItems = 0
        this.topIndex = 0
        this.topRow = 0
    }

    prepare() {
        this.setupLayout()
        this.prepareItemList()
        this.draw()
        if (this.isLunaticContents) {
            this.setupScrollable({
                maxPageItems: this.maxPageItems,
                topIndex: this.topIndex,
                topRow: this.topRow,
            })
        }
    }

    setupBindFields({ itemList }) {
        this.bindFields = getBindStrings(itemList || '')
        this.bindValues = this.getBindValues()
    }

    setupScrollable({ maxPageItems, topIndex, topRow }) {
        this.isLunaticContents = true
        this.maxPageItems = maxPageItems
        this.topIndex = topIndex
        this.topRow = topRow
        this.layout.lunaConfig.itemWidth = this.parentWindow.itemWidth()
        this.layout.lunaConfig.itemHeight = this.parentWindow.itemHeight()
        this.layout.children.forEach((child, index) => {
            child.visible = index < this.topIndex + this.maxPageItems && index >= this.topIndex
        })
    }

    scroll({ topIndex, topRow }) {
        this.updateChildren()
        this.components.forEach(component => {
            component.update()
        })
        this.topIndex = topIndex
        this.topRow = topRow
        this.layout.lunaConfig.topRow = topRow
        this.layout.children.forEach((child, index) => {
            child.visible = index < this.topIndex + this.maxPageItems && index >= this.topIndex
        })
        this.layout.setupLuna()
    }

    setupLayout() {
        if (this.layout) {
            return
        }
        this.layout = new Layout()
        this.layout.lunaName = `${this.lunaName}Layout`
        // TODO: merge maxCols & maxRows from lunaConfig
        this.layout.lunaConfig = Object.assign(
            {},
            defaultGridLayout,
            this.parentWindow && this.parentWindow instanceof Window_Selectable
                ? {
                      maxCols: this.parentWindow.maxCols(),
                      maxRows: this.parentWindow.maxPageRows(),
                      spacing: this.parentWindow.spacing(),
                  }
                : {},
        )
        this.layout.lunaConfig.maxCols = this.lunaConfig.maxCols || this.layout.lunaConfig.maxCols
        this.layout.lunaConfig.maxRows = this.lunaConfig.maxRows || this.layout.lunaConfig.maxRows
        this.layout.lunaConfig.spacing = this.lunaConfig.spacing || this.layout.lunaConfig.spacing
        this.addChild(this.layout)
        this.resizeMoveLayout(0, 0, this.width, this.height)
    }

    prepareItemList() {
        this.itemList = this.bindValues[0] || [] // FIXME: might have some better way?
        this.processItemList()
    }

    processItemList() {
        if (this.lunaConfig.limit) {
            this.itemList = this.itemList.slice(0, this.lunaConfig.limit)
        }
        if (this.lunaConfig.horizontal) {
            this.layout.lunaConfig.maxCols = Math.max(this.itemList.length, this.lunaConfig.limit)
        }
    }

    setItemList(list) {
        this.itemList = list
        this.processItemList()
        this.draw()
    }

    refresh() {
        this.components.forEach(component => component.refresh())
    }

    draw() {
        const maxItems = this.itemList.length
        this.clear()
        for (let i = 0; i < maxItems; i++) {
            const item = this.itemList[i]
            this.drawItem(item, i)
        }
        this.layout.setupLuna()
    }

    drawItem(item, index) {
        const { template, components } = this.lunaConfig
        const layout = new Layout()
        layout.lunaName = `${this.lunaName}Item${index}`
        layout.lunaConfig = Object.assign({}, template)
        template.components.forEach(componentName => {
            const componentConfig = components[componentName]
            const sprite = drawLunatic(componentConfig)
            sprite.setParentWindow(this.parentWindow)
            sprite.setpassObject(item)
            sprite.lunaName = `${layout.lunaName}${componentName}`
            sprite.lunaOriginalName = `${componentName}`
            sprite.lunaConfig = componentConfig
            // sprite.isSelect = index === this.currentIndex
            sprite.setItemIndex(index)
            sprite.setup(componentConfig)
            layout.addChild(sprite)
            this.components.push(sprite)
        })
        this.layout.addChild(layout)
    }

    clear() {
        this.layout.children.forEach(child => this.layout.removeChild(child))
        this.layout.children = []
        this.components = []
    }

    update() {
        super.update()
        this.updateChildren()
    }

    updateChildren() {
        this.layout.children.forEach((child, index) => {
            /* eslint-disable no-param-reassign */
            child.isSelect = index === this.currentIndex
            /* eslint-enable no-param-reassign */
        })
    }

    resizeMove(x, y, width, height) {
        this.resizeMoveLayout(x, y, width, height)
        super.resizeMove(x, y, width, height)
    }

    resizeMoveLayout(x, y, width, height) {
        if (this.layout.width !== width || this.layout.height !== height) {
            this.layout.resizeMove(0, 0, width, height)
            this.layout.setupLuna()
        }
    }
}
