import { ImageManager, Window_Base } from 'rmmv'
import { SpriteLunatic } from './SpriteLunatic'
import { processBindString } from '@/utils/bindData'

export class SpriteIcon extends SpriteLunatic {
    initialize() {
        super.initialize()

        this.iconSet = ''
        this.iconIndex = 0
        this.hue = 0
        this.bitmap = ImageManager.loadEmptyBitmap()
    }

    prepare({ iconSet, iconIndex, hue, scale }) {
        this.iconSet = processBindString(iconSet, this.itemIndex, this.passObject, this.parentWindow)
        this.iconIndex = parseInt(processBindString(iconIndex, this.itemIndex, this.passObject), 10)
        this.hue = hue || 0
        this.scale.x = scale ? scale.x : 1
        this.scale.y = scale ? scale.y : 1
        this.draw()
    }

    draw() {
        const { iconSet, iconIndex, hue } = this
        this.bitmap = ImageManager.loadSystem(iconSet, hue)
        const sw = Window_Base._iconWidth
        const sh = Window_Base._iconHeight
        const sx = (iconIndex % 16) * sw
        const sy = Math.floor(iconIndex / 16) * sh
        this.setFrame(sx, sy, sw, sh)
    }

    clear() {
        this.bitmap = ImageManager.loadEmptyBitmap()
    }
}
