import { Utils } from 'rmmv'

export const overrideUtils = () => {
    const { isNwjs } = Utils

    Utils.isNwjs = function() {
        const _isNwJs = isNwjs.call(this)
        if (!_isNwJs) {
            return false
        }
        try {
            __non_webpack_require__('nw.gui')
            return true
        } catch (e) {
            return false
        }
    }

    return {
        isNwjs,
    }
}
