import { Sprite, Window_Base } from 'rmmv'
import { Layout } from '@/components/Layout'
import { getAnimations } from '@/managers/LunaManager'
import * as TWEEN from '@tweenjs/tween.js'
import { safeGet } from '@/utils/safeGet'

/**
 * Container controls the whole scene layouts and configuration.
 *
 * TODO: Make utils to check if config is valid
 */
export class Container extends Sprite {
    initialize() {
        super.initialize()

        /**
         * @type {Luna.SceneConfig}
         */
        this.sceneConfig = undefined
        /**
         * @type {Layout[]}
         */
        this.layouts = []
        this.namedChildren = {}
        this.tweens = {}
        this.originalPosition = {}
    }

    /**
     * Create all things?
     */
    create() {
        if (!this.sceneConfig) {
            return
        }
        this.createLayouts()
    }

    /**
     * Create layouts?
     */
    createLayouts() {
        const layoutNames = Object.keys(this.sceneConfig.layouts)
        this.layouts = layoutNames.map(name => {
            const layoutConfig = this.sceneConfig.layouts[name]
            const layout = new Layout()
            layout.lunaConfig = layoutConfig
            layout.lunaName = name
            return layout
        })
        this.layouts.forEach(layout => this.addChild(layout))
    }

    /**
     * Call to setup both container and all components in it.
     */
    setupLuna() {
        if (!this.sceneConfig) {
            return
        }

        this.setupLayout()
        this.setupChildren()
        this.onEnterEvents()
    }

    setupLayout() {
        this.layouts.forEach(layout => {
            layout.lunaConfig.components.forEach(componentName => {
                const component = this.getComponentByName(componentName)
                if (!component) {
                    return
                }
                layout.addChild(component)
            })
        })
    }

    setupChildren() {
        Object.keys(this.namedChildren).forEach(name => {
            const component = this.getComponentByName(name)
            if (!component.lunaConfig) {
                console.warn(`[LunaEngine][${this.lunaName}] ${component.lunaName} doesn't have a config.`)
                return
            }
            component.setupLuna()
        })
    }

    onEnterEvents() {
        const onEnterEvents = this.sceneConfig.events.onSceneEnter || []
        this.handleEvents(onEnterEvents)
    }

    stopLuna() {
        if (!this.sceneConfig) {
            return
        }
        this.onExitEvents()
    }

    onExitEvents() {
        const onExitEvents = this.sceneConfig.events.onExitEvents || []
        this.handleEvents(onExitEvents)
    }

    onItemSelectEvents(window, index) {
        const events = safeGet(this.sceneConfig, ['events', 'onItemSelect', window.lunaName])
        if (!events) {
            return
        }
        this.handleEvents(events.map(event => ({ ...event, target: event.target.replace(`\${index}`, `${index}`) })))
    }

    onItemUnselectEvents(window, index) {
        const events = safeGet(this.sceneConfig, ['events', 'onItemUnselect', window.lunaName])
        if (!events) {
            return
        }
        this.handleEvents(events.map(event => ({ ...event, target: event.target.replace(`\${index}`, `${index}`) })))
    }

    onComponentShowEvents(window) {
        const events = safeGet(this.sceneConfig, ['events', 'onComponentShow', window.lunaName])
        if (!events) {
            return
        }
        const tween = this.tweens[window.lunaName]
        if (window.hideAfterAnimations) {
            if (tween) {
                tween.onComplete(() => {}).onStop(() => {})
                tween.stop()
            }
            window.hideAfterAnimations = false
            window.doneHiddenAnimation = false
            window.visible = true
        }
        this.handleEvents(events)
    }

    onComponentHideEvents(window) {
        const events = safeGet(this.sceneConfig, ['events', 'onComponentHide', window.lunaName])
        if (!events) {
            return
        }
        this.handleEvents(events)
        if (this.tweens[window.lunaName]) {
            window.doneHiddenAnimation = true
            window.hideAfterAnimations = true
            const onCompleteOld = this.tweens[window.lunaName]._onCompleteCallback
            this.tweens[window.lunaName]
                .onComplete(() => {
                    onCompleteOld()
                    window.hideAfterAnimations = false
                    window.hide()
                })
                .onStop(() => {
                    window.hideAfterAnimations = false
                    window.hide()
                })
        }
    }

    handleEvents(events) {
        events.forEach(event => {
            switch (event.type) {
                case 'Animation':
                    return this.chainingAnimationEvent(event)
                default:
                    throw new Error(`[LunaEngine] Event type ${event.type} not supported`)
            }
        })
    }

    update() {
        super.update()
        TWEEN.update()
    }

    /**
     * Get config for component from container config
     *
     * @param {any} component
     */
    getComponentLunaConfig(component) {
        if (component.lunaName) {
            return this.sceneConfig.components[component.lunaName]
        }
        return undefined
    }

    /**
     * Get config for component from container config
     *
     * @param {string} componentName
     */
    getComponentLunaConfigByName(componentName) {
        return this.sceneConfig.components[componentName]
    }

    /**
     * Get component from children list by Luna name.
     *
     * @param {string} componentName
     */
    getComponentByName(componentName) {
        return this.namedChildren[componentName]
    }

    addChild(child) {
        super.addChild(child)
        this.addNamedChild(child)
    }

    addNamedChild(child) {
        if (child.lunaName) {
            this.namedChildren[child.lunaName] = child
            this.namedChildren[child.lunaName].sceneContainer = this
        }
    }

    removeNamedChild(child) {
        if (child.lunaName) {
            this.namedChildren[child.lunaName] = null
            delete this.namedChildren[child.lunaName]
        }
    }

    isAnimating() {
        return Object.values(this.tweens).some(tween => !!tween)
    }

    chainingAnimationEvent(event) {
        const tween = this.tweens[event.target]
        const newTween = this.animationEvent(event)
        if (tween) {
            tween._onCompleteCallback = null
            tween.stop()
        }
        if (!newTween) {
            return
        }
        newTween.start()
        this.tweens[event.target] = newTween
    }

    animationEvent(event) {
        const target = this.namedChildren[event.target]
        if (!target) {
            return null
        }
        const layout = target.parent
        const animation = getAnimations()[event.animation]
        const startAnimation = animation[0]
        const endAnimation = animation[100]
        const duration = event.duration || animation.duration
        const delay = event.delay || animation.delay
        const repeat = event.repeat || animation.repeat
        const yoyo = event.yoyo || animation.yoyo
        const originalX = target instanceof Window_Base ? target.x + target.anchor.x * target.width : target.x
        const originalY = target instanceof Window_Base ? target.y + target.anchor.y * target.height : target.y
        const ease = event.ease || animation.ease || 'Quadratic.Out'
        const force = event.force || animation.force
        const oldTween = this.tweens[event.target]

        if (!(layout instanceof Layout)) {
            console.warn(`Target is not in a layout (target, layout)`, target, layout)
            return null
        }

        if (!this.originalPosition[event.target]) {
            this.originalPosition[event.target] = {
                x: originalX,
                y: originalY,
            }
        }

        // TODO: Keyframes here
        const animationState = {
            x: originalX,
            y: originalY,
            alpha: target.alpha,
        }
        const targetAnimation = {
            x:
                this.originalPosition[event.target].x +
                layout.calculatePositionValue(target, endAnimation.offsetX, 'horizontal'),
            y:
                this.originalPosition[event.target].y +
                layout.calculatePositionValue(target, endAnimation.offsetY, 'vertical'),
            alpha: endAnimation.alpha || 1,
        }

        if ((!oldTween && startAnimation) || force) {
            animationState.x += layout.calculatePositionValue(target, startAnimation.offsetX, 'horizontal')
            animationState.y += layout.calculatePositionValue(target, startAnimation.offsetY, 'vertical')
            animationState.alpha = startAnimation.alpha
        }

        // Setup starting point
        target.resizeMove(animationState.x, animationState.y)
        target.alpha = animationState.alpha
        const tweenEasing = ease.split('.').reduce((prev, cur) => prev[cur], TWEEN.Easing)

        const tween = new TWEEN.Tween(animationState)
            .to(targetAnimation, duration)
            .easing(tweenEasing)
            .delay(delay || 0)
            .onUpdate(state => {
                target.resizeMove(state.x, state.y)
                target.alpha = state.alpha
            })
            .onComplete(() => {
                // this.tweens[event.target].stop()
                this.tweens[event.target] = null
            })

        if (repeat) {
            tween.repeat(Infinity)

            if (yoyo) {
                tween.yoyo(true)
            }
        }
        return tween
    }
}
