/**
 * Map default Scenes and Windows
 */
const configName = {
    // Scene Title
    Scene_Title: 'SceneTitle',
    Window_TitleCommand: 'TitleCommand',

    // Scene Map
    Scene_Map: 'SceneMap',
    Window_MapName: 'MapName',

    // Scene Menu
    Scene_Menu: 'SceneMenu',
    Window_MenuCommand: 'MenuCommand',
    Window_Gold: 'Gold',
    Window_MenuStatus: 'MenuStatus',

    // Scene Item
    Scene_Item: 'SceneItem',
    Window_ItemCategory: 'ItemCategory',
    Window_ItemList: 'ItemList',

    // Scene Skill
    Scene_Skill: 'SceneSkill',
    Window_SkillType: 'SkillType',
    Window_SkillList: 'SkillList',
    Window_SkillStatus: 'SkillStatus',

    // Scene Equip
    Scene_Equip: 'SceneEquip',
    Window_EquipStatus: 'EquipStatus',
    Window_EquipCommand: 'EquipCommand',
    Window_EquipSlot: 'EquipSlot',
    Window_EquipItem: 'EquipItem',

    // Scene Battle
    Scene_Battle: 'SceneBattle',
    Window_BattleStatus: 'BattleStatus',
    Window_PartyCommand: 'PartyCommand',
    Window_ActorCommand: 'ActorCommand',
    Window_BattleLog: 'BattleLog',
    Window_BattleSkill: 'BattleSkill',
    Window_BattleItem: 'BattleItem',
    Window_BattleActor: 'BattleActor',
    Window_BattleEnemy: 'BattleEnemy',

    // Scene Status
    Scene_Status: 'SceneStatus',
    Window_StatusCommand: 'StatusCommand',
    Window_StatusBasic: 'StatusBasic',
    Window_StatusInfo: 'StatusInfo',
    Window_Status: 'Status',

    // Scene Name
    Scene_Name: 'SceneName',
    Window_NameEdit: 'NameEdit',
    Window_NameInput: 'NameInput',

    // Scene Shop
    Scene_Shop: 'SceneShop',
    Window_ShopCommand: 'ShopCommand',
    Window_ShopNumber: 'ShopNumber',
    Window_ShopStatus: 'ShopStatus',
    Window_ShopBuy: 'ShopBuy',
    Window_ShopSell: 'ShopSell',

    // general
    Window_Help: 'Help',
    Window_Message: 'Message',
    Window_ScrollText: 'ScrollText',
    Window_MenuActor: 'MenuActor',
}

/**
 * Map constructor name to config name
 *
 * @param {string} constructorName
 * @return {string}
 */
export const mapConfigName = constructorName => {
    if (configName[constructorName]) {
        return configName[constructorName]
    }
    return constructorName.replace('Window_', '').replace('_', '')
}
