import { Scene_Shop, Graphics } from 'rmmv'
import { Window_ShopDummy } from '@/windows/WindowShopDummy'

export const overrideSceneShop = () => {
    const { createDummyWindow } = Scene_Shop.prototype

    Scene_Shop.prototype.createDummyWindow = function() {
        const wy = this._commandWindow.y + this._commandWindow.height
        const wh = Graphics.boxHeight - wy
        this._dummyWindow = new Window_ShopDummy(0, wy, Graphics.boxWidth, wh)
        this.addWindow(this._dummyWindow)
    }

    // returns alias
    return {
        createDummyWindow,
    }
}
