/**
 * Contains all alias methods.
 */
export const alias = {}

/**
 * Mixin methods to RMMV classes and modules.
 */
export const mixinRMMV = () => {
    alias.DataManager = overrideDataManager()
    alias.SceneManager = overrideSceneManager()
    alias.Utils = overrideUtils()
    alias.Graphics = overrideGraphics()
    alias.Bitmap = overrideBitmap()
    alias.Game_Actor = overrideGameActor()
    alias.Sprite = overrideSprite()
    alias.Sprite_Battler = overrideSpriteBattler()
    alias.Sprite_Actor = overrideSpriteActor()
    alias.Sprite_Animation = overrideSpriteAnimation()
    alias.Scene_Base = overrideSceneBase()
    alias.Scene_Title = overrideSceneTitle()
    alias.Scene_MenuBase = overrideSceneMenuBase()
    alias.Scene_ItemBase = overrideSceneItemBase()
    alias.Scene_Battle = overrideSceneBattle()
    alias.Scene_Status = overrideSceneStatus()
    alias.Scene_Shop = overrideSceneShop()
    alias.Window = overrideWindow()
    alias.Window_Base = overrideWindowBase()
    alias.Window_Selectable = overrideWindowSelectable()
    alias.Window_Command = overrideWindowCommand()
    alias.Window_BattleLog = overrideWindowBattleLog()
    alias.Window_ActorCommand = overrideWindowActorCommand()
    alias.Window_NumberInput = overrideWindowNumberInput()
}

export const compatibleYEP = () => {
    alias.YEP_BattleEngineCore = compatibleYEPBattleEngineCore()
    alias.YEP_X_VisualHpGauge = compatibleYEPXVisualHpGauge()
}

/**
 * Bootstrap Luna Engine.
 *
 * All loading, querying, parsing are put here.
 */
export const bootstrap = () => {
    const { create, isReady, start } = Scene_Boot.prototype

    Scene_Boot.prototype.create = function() {
        this.checkDependencies()
        this.loadLunaEngine()
        create.call(this)
    }

    Scene_Boot.prototype.checkDependencies = function() {
        // checkYanflyDependency('Core', 1.3)
        // checkYanflyDependency('Status', 1.04)
    }

    Scene_Boot.prototype.loadLunaEngine = function() {
        console.log('[LunaEngine] Start loading Luna Engine.')
        registerDefaultPreload()
        preloadResources()
        loadLunaConfig('general')
        loadLunaConfig('presets')
        loadLunaConfig('animations')
        getSaveData()
        Object.keys(Scenes).forEach(sceneName => loadLunaConfig(mapConfigName(sceneName)))
    }

    Scene_Boot.prototype.isReady = function() {
        return isReady.call(this) && !isLoading()
    }

    Scene_Boot.prototype.start = function() {
        start.call(this)

        // Welcome welcome babe
        console.log('[LunaEngine] All configs loaded.')
    }
}
