import { Window_BattleLog } from 'rmmv'
import { getImprovements } from '@/managers'

export const overrideWindowBattleLog = () => {
    const { initialize, maxLines, drawBackground, refresh } = Window_BattleLog.prototype

    Window_BattleLog.prototype.initialize = function() {
        initialize.call(this)
        if (!getImprovements().BattleLogDefaultBackground) {
            this.opacity = 255
        }
    }

    Window_BattleLog.prototype.maxLines = function() {
        return Math.floor((this.height - this.standardPadding() * 2) / this.lineHeight()) || maxLines.call(this)
    }

    Window_BattleLog.prototype.drawBackground = function() {
        if (!getImprovements().BattleLogDefaultBackground) {
            return
        }
        drawBackground.call(this)
    }

    Window_BattleLog.prototype.refresh = function() {
        refresh.call(this)
        if (this._lines.length + this._baseLineStack.length > 0) {
            this.opacity = 255
            return
        }
        this.opacity = 0
    }

    return { maxLines, drawBackground }
}
