import { SceneManager } from 'rmmv'

export const overrideSceneManager = () => {
    const { isCurrentSceneBusy } = SceneManager

    SceneManager.getContainer = () => SceneManager._scene.sceneContainer

    SceneManager.isCurrentSceneBusy = function() {
        return isCurrentSceneBusy.call(this) || (this._scene && this._scene.isAnimating())
    }

    SceneManager.getWindowList = () => SceneManager._scene.windowNameList

    return {}
}
