import { Window_Selectable } from 'rmmv'
import { getValue } from '@/utils'
import { getPresets } from '@/managers/LunaManager'
import { defaultLunaticGrid } from '@/utils/defaultConfig'
import { drawLunatic } from '@/utils/drawLunatic'

export const overrideWindowSelectable = () => {
    const {
        initialize,
        maxCols,
        setTopRow,
        spacing,
        update,
        select,
        setupLuna,
        ensureCursorVisible,
    } = Window_Selectable.prototype

    Window_Selectable.prototype.initialize = function(x, y, width, height) {
        if (this.maxCols) {
            this.maxCols = Window_Selectable.prototype.maxCols.bind(this)
        }

        if (this.makeItemList) {
            const _makeItemList = this.makeItemList
            this.makeItemList = () => {
                _makeItemList.call(this)
                if (this.lunaticGridItems) {
                    this.lunaticGridItems.setItemList(this._data)
                }
            }
        }

        initialize.call(this, x, y, width, height)
        this._oldIndex = -1
    }

    Window_Selectable.prototype.maxCols = function() {
        if (!this.lunaConfig) {
            return maxCols.call(this)
        }
        const value = getValue('columns', this.lunaConfig, getPresets())
        return (value === 'max' ? this.maxItems() || 1 : value) || maxCols.call(this)
    }

    Window_Selectable.prototype.spacing = function() {
        if (!this.lunaConfig) {
            return spacing.call(this)
        }
        return getValue('spacing', this.lunaConfig, getPresets())
    }

    Window_Selectable.prototype.setupLuna = function() {
        setupLuna.call(this)
        this.animateSelect(this.index())
    }

    Window_Selectable.prototype.drawLunaticItems = function() {
        if (this.lunaticGridItems && this.lunaticGridItems.passObject.length === this.bindItemList.length) {
            this.contentsLayout.addChild(this.lunaticGridItems)
            this.lunaticGridItems.refresh()
            this.scrollLunatic()
            return
        }
        const gridConfig = Object.assign({}, defaultLunaticGrid, this.lunaLunatic.lunaticItems, {
            components: this.lunaLunatic.components,
        })
        const sprite = drawLunatic(gridConfig)
        sprite.lunaConfig = gridConfig
        sprite.lunaName = `${this.lunaName}Lunatic`
        sprite.setParentWindow(this)
        sprite.setpassObject(this.bindItemList)
        sprite.setup(gridConfig)
        sprite.setupScrollable({ maxPageItems: this.maxPageItems(), topIndex: this.topIndex(), topRow: this.topRow() })
        this.lunaticGridItems = sprite
        if (this._data) {
            this.lunaticGridItems.setItemList(this._data)
        }
        this.contentsLayout.addChild(sprite)
        this.scrollLunatic()
        // this.animateSelect(this.index())
    }

    Window_Selectable.prototype.select = function(index) {
        select.call(this, index)
        this.animateSelect(index)
        if (this.lunaticGridItems) {
            this.lunaticGridItems.currentIndex = index
        }
    }

    Window_Selectable.prototype.scrollLunatic = function() {
        if (this.lunaticGridItems) {
            this.lunaticGridItems.currentIndex = this.index()
            this.lunaticGridItems.scroll({ topIndex: this.topIndex(), topRow: this.topRow() })
        }
    }

    Window_Selectable.prototype.setTopRow = function(row) {
        if (!this.isLunaticMode) {
            setTopRow.call(this, row)
            return
        }
        const scrollY = row.clamp(0, this.maxTopRow()) * this.itemHeight()
        if (this._scrollY !== scrollY) {
            this._scrollY = scrollY
            if (!this.isLunaticMode || this.isLunaticAddition) {
                this.refresh()
            }
            this.scrollLunatic()
            this.updateCursor()
        }
    }

    Window_Selectable.prototype.animateSelect = function(index) {
        const oldIndex = this._oldIndex
        if (!this.sceneContainer) {
            return
        }
        if (oldIndex !== index && oldIndex >= 0) {
            this.sceneContainer.onItemUnselectEvents(this, oldIndex)
        }
        if (index >= 0) {
            this.sceneContainer.onItemSelectEvents(this, index)
        }
        this._oldIndex = index
    }

    return {
        maxCols,
        update,
        select,
        ensureCursorVisible,
        setTopRow,
    }
}
