import { Graphics, Sprite_Enemy, Window_BattleLog, Window_Base } from 'rmmv'

export const compatibleYEPBattleEngineCore = () => {
    const { Window_EnemyVisualSelect, Yanfly, Imported } = window

    if (!Imported || !Imported.YEP_BattleEngineCore) {
        return {}
    }

    const _Window_EnemyVisualSelect_updateOpacity = Window_EnemyVisualSelect.prototype.updateOpacity
    Window_EnemyVisualSelect.prototype.updateOpacity = function() {
        _Window_EnemyVisualSelect_updateOpacity.call(this)
        if (this.isShowWindow()) {
            this.opacity += 32
        } else {
            this.opacity -= 32
        }
    }

    const _Window_EnemyVisualSelect_makeWindowBoundaries = Window_EnemyVisualSelect.prototype.makeWindowBoundaries
    Window_EnemyVisualSelect.prototype.makeWindowBoundaries = function() {
        if (!this._requestRefresh) return
        this._minX = -1 * this.standardPadding()
        this._maxX = Graphics.boxWidth - this.width + this.standardPadding()
        this._minY = -1 * this.standardPadding()
        this._maxY = Graphics.boxHeight - this.height + this.standardPadding()
    }

    const _Window_EnemyVisualSelect_updateWindowSize = Window_EnemyVisualSelect.prototype.updateWindowSize
    Window_EnemyVisualSelect.prototype.updateWindowSize = function() {
        const spriteWidth = this._battler.spriteWidth()
        this.contents.fontSize = Yanfly.Param.BECEnemyFontSize
        if (this._nameTextWidth === undefined) {
            this._nameTextWidth = this.textWidth(this._battler.name())
        }
        let textWidth = this._nameTextWidth
        textWidth += this.textPadding() * 2
        let width = Math.max(spriteWidth, textWidth) + this.standardPadding() * 2
        width = Math.ceil(width)
        const height = this.lineHeight() + this.standardPadding() * 2
        if (width === this.width && height === this.height) return
        this.width = width
        this.height = height
        this.createContents()
        this._requestRefresh = true
        this.makeWindowBoundaries()
    }

    const _Window_EnemyVisualSelect_updateWindowPosition = Window_EnemyVisualSelect.prototype.updateWindowPosition
    Window_EnemyVisualSelect.prototype.updateWindowPosition = function() {
        _Window_EnemyVisualSelect_updateWindowPosition.call(this)
        this.x += this.lunaConfig.offset.x
        this.y += this.lunaConfig.offset.y
        if (!this.customBackgroundSprite) {
            return
        }
        this.customBackgroundSprite.x = (this.width - this.customBackgroundSprite.width) / 2
        this.customBackgroundSprite.y = (this.height - this.customBackgroundSprite.height) / 2
    }

    const _Sprite_Enemy_addVisualSelectWindow = Sprite_Enemy.prototype.addVisualSelectWindow
    Sprite_Enemy.prototype.addVisualSelectWindow = function() {
        _Sprite_Enemy_addVisualSelectWindow.call(this)
        if (this._visualSelectWindow) {
            this._visualSelectWindow.setupLuna()
        }
    }

    const _Window_BattleLog_drawSimpleActionLine = Window_BattleLog.prototype.drawSimpleActionLine
    Window_BattleLog.prototype.drawSimpleActionLine = function(index) {
        const text = this._lines[index].replace('<SIMPLE>', '')
        const rect = this.itemRectForText(index)
        this.contents.clearRect(rect.x, rect.y, rect.width, rect.height)
        if (this._actionIcon) {
            const tw = this.textWidth(text)
            const ix = (rect.width - tw - Window_Base._iconWidth) / 2 - 4
            this.drawIcon(this._actionIcon, ix, rect.y + 2)
        }
        this.drawText(text, rect.x, rect.y, rect.width, 'center')
    }

    // returns alias
    return {
        _Window_EnemyVisualSelect_updateOpacity,
        _Window_EnemyVisualSelect_makeWindowBoundaries,
        _Window_EnemyVisualSelect_updateWindowSize,
        _Window_EnemyVisualSelect_updateWindowPosition,
        _Sprite_Enemy_addVisualSelectWindow,
        _Window_BattleLog_drawSimpleActionLine,
    }
}
