import { Window_Command } from 'rmmv'

export const overrideWindowCommand = () => {
    const { addCommand, clearCommandList, refreshLunatic } = Window_Command.prototype

    Window_Command.prototype.clearCommandList = function() {
        clearCommandList.call(this)
        this.bindItemList = []
    }

    Window_Command.prototype.addCommand = function(name, symbol, enabled, ext) {
        addCommand.call(this, name, symbol, enabled, ext)
        this.bindItemList.push({ name, symbol, enabled, ext })
    }

    Window_Command.prototype.refreshLunatic = function() {
        this.clearCommandList()
        this.makeCommandList()
        refreshLunatic.call(this)
    }

    return { addCommand, clearCommandList, refreshLunatic }
}
