import { ImageManager } from 'rmmv'
import { SpriteLunatic } from './SpriteLunatic'
import { processBindString } from '@/utils/bindData'

export class SpriteImage extends SpriteLunatic {
    initialize() {
        super.initialize()

        this.imageName = ''
        this.hue = 0
        this.sourceRect = {
            width: {
                type: 'percentage',
                value: 100,
            },
            height: {
                type: 'percentage',
                value: 100,
            },
            x: {
                type: 'percentage',
                value: 0,
            },
            y: {
                type: 'percentage',
                value: 0,
            },
        }
        this.verticalAtlas = 0
        this.bitmap = ImageManager.loadEmptyBitmap()
    }

    prepare({ imageName, hue, sourceRect, scale, verticalAtlas = 0 }) {
        this.imageName = processBindString(imageName, this.itemIndex, this.passObject, this.parentWindow)
        this.hue = hue || 0
        this.verticalAtlas = verticalAtlas
        this.sourceRect = verticalAtlas ? this.calculateVerticalAtlas() : sourceRect || this.sourceRect
        this.scale.x = scale ? scale.x : 1
        this.scale.y = scale ? scale.y : 1
        this.draw()
    }

    draw() {
        const { imageName, hue } = this
        this.bitmap = ImageManager.loadSystem(imageName, hue)
        this.setupRect()
    }

    clear() {
        this.bitmap = ImageManager.loadEmptyBitmap()
    }

    setupRect() {
        const width = this.calculateSize({ ...this.sourceRect.width, axis: 'horizontal' })
        const height = this.calculateSize({ ...this.sourceRect.height, axis: 'vertical' })
        const x = this.calculateSize({ ...this.sourceRect.x, axis: 'horizontal' })
        const y = this.calculateSize({ ...this.sourceRect.y, axis: 'vertical' })
        this.setFrame(x, y, width, height)
    }

    calculateSize({ type, value, axis }) {
        if (type === 'percentage') {
            return (axis === 'vertical' ? this.bitmap.height * value : this.bitmap.width * value) / 100
        }
        return value
    }

    calculateVerticalAtlas() {
        return {
            width: {
                type: 'percentage',
                value: 100,
            },
            height: {
                type: 'percentage',
                value: 100 / this.verticalAtlas,
            },
            x: {
                type: 'percentage',
                value: 0,
            },
            y: {
                type: 'percentage',
                value: (100 / this.verticalAtlas) * this.itemIndex,
            },
        }
    }
}
