import { ImageManager, Window, Bitmap } from 'rmmv'
import { getImprovements } from '@/managers/LunaManager'

export const overrideWindow = () => {
    const { initialize, _refreshFrame, _refreshCursor, _updateCursor } = Window.prototype

    Window.prototype.initialize = function() {
        initialize.call(this)

        this.enableCursor = true
        this.cursorBlink = true
        this.customCursor = {
            enable: false,
            image: '',
        }
        this.cursorOffset = {
            x: 0,
            y: 0,
        }
        this.windowSkinTileMode = getImprovements().WindowSkinTileMode
    }

    Window.prototype._refreshFrame = function() {
        if (this.windowSkinTileMode === 'stretch') {
            _refreshFrame.call(this)
        }

        if (this.windowSkinTileMode === 'tile') {
            this._refreshTileFrame()
        }
    }

    Window.prototype._refreshTileFrame = function() {
        const w = this._width
        const h = this._height
        const m = 24
        const bitmap = new Bitmap(w, h)

        this._windowFrameSprite.bitmap = bitmap
        this._windowFrameSprite.setFrame(0, 0, w, h)

        if (w > 0 && h > 0 && this._windowskin) {
            const skin = this._windowskin
            const p = 96
            const q = 96

            const tileSize = p - m * 2
            const hEdge = w - m * 2
            const vEdge = h - m * 2

            const hTiles = Math.floor(hEdge / tileSize)
            const vTiles = Math.floor(vEdge / tileSize)

            const hRemain = hEdge % tileSize
            const vRemain = vEdge % tileSize

            // Top Side
            for (let i = 0; i < hTiles; i++) {
                bitmap.blt(skin, p + m, 0, tileSize, m, m + i * tileSize, 0, tileSize, m)
            }
            bitmap.blt(skin, p + m, 0, hRemain, m, m + tileSize * hTiles, 0, hRemain, m)

            // Bottom Side
            for (let i = 0; i < hTiles; i++) {
                bitmap.blt(skin, p + m, q - m, tileSize, m, m + i * tileSize, h - m, tileSize, m)
            }
            bitmap.blt(skin, p + m, q - m, hRemain, m, m + tileSize * hTiles, h - m, hRemain, m)

            // Left Side
            for (let i = 0; i < vTiles; i++) {
                bitmap.blt(skin, p, m, m, tileSize, 0, m + i * tileSize, m, tileSize)
            }
            bitmap.blt(skin, p, m, m, vRemain, 0, m + vTiles * tileSize, m, vRemain)

            // Right Side
            for (let i = 0; i < vTiles; i++) {
                bitmap.blt(skin, p + q - m, m, m, tileSize, w - m, m + i * tileSize, m, tileSize)
            }
            bitmap.blt(skin, p + q - m, m, m, vRemain, w - m, m + vTiles * tileSize, m, vRemain)

            // Top-Left Corner
            bitmap.blt(skin, p + 0, 0 + 0, m, m, 0, 0, m, m)
            // Top-Right Corner
            bitmap.blt(skin, p + q - m, 0 + 0, m, m, w - m, 0, m, m)
            // Bottom-Left Corner
            bitmap.blt(skin, p + 0, 0 + q - m, m, m, 0, h - m, m, m)
            // Bottom-Right Corner
            bitmap.blt(skin, p + q - m, 0 + q - m, m, m, w - m, h - m, m, m)
        }
    }

    Window.prototype._refreshCursor = function() {
        if (!this.customCursor.enable) {
            _refreshCursor.call(this)
            this._updateCursorPosition()
            return
        }

        this._windowCursorSprite.bitmap = ImageManager.loadSystem(this.customCursor.image)
        this._updateCursorPosition()
    }

    Window.prototype._updateCursor = function() {
        _updateCursor.call(this)
        this._windowCursorSprite.visible = this._isCursorVisible()
        if (!this.cursorBlink) {
            this._windowCursorSprite.alpha = this.contentsOpacity / 255
        }
        if (!this.enableCursor) {
            this._windowCursorSprite.visible = false
        }
    }

    Window.prototype._updateCursorPosition = function() {
        const pad = this._padding
        const x = this._cursorRect.x + pad - this.origin.x
        const y = this._cursorRect.y + pad - this.origin.y
        const x2 = Math.max(x, pad) + this.cursorOffset.x
        const y2 = Math.max(y, pad) + this.cursorOffset.y
        this._windowCursorSprite.move(x2, y2)
    }

    Window.prototype._isCursorVisible = function() {
        return this.isOpen() && this._cursorRect.width > 0 && this._cursorRect.height > 0
    }

    return {
        initialize,
        _refreshFrame,
        _refreshCursor,
        _updateCursor,
    }
}
