import { Graphics, Sprite } from 'rmmv'
import { getValue } from '@/utils'
import { getPresets } from '@/managers/LunaManager'

/**
 * Manages layout and positions for every components inside it.
 */
export class Layout extends Sprite {
    initialize() {
        super.initialize()

        /**
         * @type {Luna.ComponentConfig}
         */
        this.lunaConfig = undefined
        this.lunaName = ''

        // default layout?
        this.setFrame(0, 0, Graphics.width, Graphics.height)
    }

    isFillable() {
        const layoutType = this.lunaConfig.type
        return layoutType === 'HorizontalLayout' || layoutType === 'VerticalLayout'
    }

    lineHeight() {
        return getValue('lineHeight', this.lunaConfig, getPresets())
    }

    standardPadding() {
        return getValue('windowPadding', this.lunaConfig, getPresets())
    }

    fittingHeight(lines) {
        return lines * this.lineHeight() + this.standardPadding() * 2
    }

    /**
     * Setup all Luna things
     */
    setupLuna() {
        this.setupLayout()
        this.setupChildren()
    }

    getChildren() {
        return this.children.filter(child => child.lunaConfig)
    }

    /**
     * Setup Layout and Positions of all children.
     */
    setupLayout() {
        const layoutType = this.lunaConfig.type

        switch (layoutType) {
            case 'FreeLayout':
                return this.setupFreeLayout()
            case 'GridLayout':
                return this.setupGridLayout()
            case 'VerticalLayout':
            case 'HorizontalLayout':
                return this.setupFillableLayout()
            default:
                throw new Error(`[LunaEngine][${this.lunaName}] Layout type ${layoutType} not supported`)
        }
    }

    /**
     * FreeLayout boiz.
     */
    setupFreeLayout() {
        this.getChildren().forEach(child => {
            const config = child.lunaConfig

            const x = this.calculatePosition(child, config, 'x')
            const y = this.calculatePosition(child, config, 'y')
            const width = config.width ? this.calculatePosition(child, config, 'width') : child.width
            const height = config.height ? this.calculatePosition(child, config, 'height') : child.height

            if (config.anchor) {
                child.setAnchor(config.anchor.x, config.anchor.y)
            }

            child.resizeMove(x, y, width, height)
        })
    }

    setupGridLayout() {
        this.getChildren().forEach((child, index) => {
            const config = child.lunaConfig

            const offsetX = config.x ? this.calculatePosition(child, config, 'x') : 0
            const offsetY = config.y ? this.calculatePosition(child, config, 'y') : 0

            const maxCols = this.lunaConfig.maxCols || 1
            const maxRows = this.lunaConfig.maxRows || 1
            const topRow = this.lunaConfig.topRow || 0
            const spacing = this.lunaConfig.spacing || 0
            const width = this.lunaConfig.itemWidth || (this.width + spacing) / maxCols - spacing
            const height = this.lunaConfig.itemHeight || (this.height + spacing) / maxRows - spacing
            const x = (index % maxCols) * (width + spacing) + offsetX
            const y = Math.floor(index / maxCols) * height + offsetY - topRow * height

            if (config.anchor) {
                child.setAnchor(config.anchor.x, config.anchor.y)
            }

            child.resizeMove(x, y, width, height)
        })
    }

    /**
     * Horizontal Layout & Vertical Layout sir.
     */
    setupFillableLayout() {
        const fillableSizes = this.calculateFillableSizes()
        const offsets = { x: 0, y: 0 }
        const isHorizontal = this.lunaConfig.type === 'HorizontalLayout'
        const isVertical = this.lunaConfig.type === 'VerticalLayout'

        this.getChildren().forEach(child => {
            const isFillWidth = child.lunaConfig.width.type === 'fill'
            const isFillHeight = child.lunaConfig.height.type === 'fill'
            const config = child.lunaConfig

            const x = this.calculatePosition(child, config, 'x')
            const y = this.calculatePosition(child, config, 'y')
            const width = isFillWidth
                ? fillableSizes.width * config.width.value
                : this.calculatePosition(child, config, 'width')
            const height = isFillHeight
                ? fillableSizes.height * config.height.value
                : this.calculatePosition(child, config, 'height')

            const realX = x + (isHorizontal ? offsets.x : 0)
            const realY = y + (isVertical ? offsets.y : 0)

            child.resizeMove(realX, realY, width, height)

            offsets.x += isHorizontal ? x + width : 0
            offsets.y += isVertical ? y + height : 0
        })
    }

    calculateFillableSizes() {
        const fillableSizes = { width: this.width, height: this.height }
        const isHorizontal = this.lunaConfig.type === 'HorizontalLayout'
        const isVertical = this.lunaConfig.type === 'VerticalLayout'
        let fillComponentNumber = 0

        this.getChildren().forEach(child => {
            const isFillWidth = child.lunaConfig.width.type === 'fill'
            const isFillHeight = child.lunaConfig.height.type === 'fill'
            const config = child.lunaConfig

            // const x = this.calculatePosition(child, config, 'x')
            // const y = this.calculatePosition(child, config, 'y')
            const width = isFillWidth ? 0 : this.calculatePosition(child, config, 'width')
            const height = isFillHeight ? 0 : this.calculatePosition(child, config, 'height')

            fillableSizes.width -= isHorizontal ? width : 0
            fillableSizes.height -= isVertical ? height : 0

            let fillNumber = 0
            if (isFillWidth) {
                fillNumber = config.width.value
            }
            if (isFillHeight) {
                fillNumber = config.height.value
            }
            fillComponentNumber += fillNumber
        })

        fillableSizes.width /= Math.round(isHorizontal ? fillComponentNumber : 1)
        fillableSizes.height /= Math.round(isVertical ? fillComponentNumber : 1)

        return fillableSizes
    }

    /**
     * Setup Luna all children.
     */
    setupChildren() {
        this.getChildren().forEach(child => child.setupLuna())
    }

    /**
     * @param {any} component
     * @param {'x'|'y'|'width'|'height'} position
     */
    calculatePosition(component, config, position) {
        const positionValue = config[position]
        const axis = ['x', 'width'].indexOf(position) >= 0 ? 'horizontal' : 'vertical'

        const value = this.calculatePositionValue(component, positionValue, axis)

        let offset = 0
        if (position === 'x' && config.offsetX) {
            offset = this.calculatePositionValue(component, config.offsetX, axis)
        }
        if (position === 'y' && config.offsetY) {
            offset = this.calculatePositionValue(component, config.offsetY, axis)
        }

        return value + offset
    }

    /**
     * @param {any} component
     * @param {Luna.PositionValue} positionValue
     * @param {'horizontal'|'vertical'} axis
     */
    calculatePositionValue(component, positionValue, axis) {
        if (!positionValue) {
            return 0
        }

        if (positionValue.type === 'fixed') {
            return positionValue.value
        }

        if (positionValue.type === 'percentage') {
            return Math.round(
                axis === 'horizontal'
                    ? (positionValue.value * this.width) / 100
                    : (positionValue.value * this.height) / 100,
            )
        }

        if (positionValue.type === 'fitLines') {
            return component.fittingHeight(positionValue.value)
        }

        if (positionValue.type === 'lineHeight') {
            return component.lineHeight() * positionValue.value
        }

        if (positionValue.type === 'fill') {
            if (!this.isFillable()) {
                throw new Error(
                    `[LunaEngine][${component.lunaName}] Position type ${
                        positionValue.type
                    } is only supported in HorizontalLayout and VerticalLayout.`,
                )
            }
            return positionValue.value
        }

        throw new Error(`[LunaEngine] Position type ${positionValue.type} is not supported.`)
    }

    resizeMove(x, y, width, height) {
        this.move(x, y)
        if (width) {
            this.width = width
        }
        if (height) {
            this.height = height
        }
    }
}
